<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Entrega Remesa</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>

                       <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="primary" dark class="mb-2">Nuevo</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                
                                 <v-flex xs12 sm12 md12>
                                    <v-autocomplete v-model="id_empleado"
                                        :items="empleados" 
                                        label="Empleado"
                                        :search-input.sync="search1"
                                        v-on:keyup="FiltroEmpleados"
                                        autocomplete>
                                    </v-autocomplete>
                                </v-flex>
                                <v-flex xs6 sm6 md6>
                                    <v-text-field type="number" v-model="digitacion" label="Digitacion" v-on:change="calcular" requerid></v-text-field>
                                </v-flex>

                                <v-flex xs6 sm6 md6>
                                    <v-text-field type="number" v-model="despacho" label="Despacho" v-on:change="calcular" requerid></v-text-field>
                                </v-flex>

                                <v-flex xs6 sm6 md6>
                                    <v-text-field type="number" v-model="sobre" label="Sobre" v-on:change="calcular" requerid></v-text-field>
                                </v-flex>

                                <v-flex xs6 sm6 md6>
                                     <v-text-field type="number" v-model="cantidad" label="Total" requerid readonly></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                     <v-textarea v-model="observacion" label="Observacion"></v-textarea>
                                </v-flex>
                             
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>              
                </v-toolbar>


            <v-data-table
                :headers="headers"
                :items="EntregaRemesas"
                :search="search"
                class="elevation-1"
            >
                <template slot="items" slot-scope="props"> 
                     <td class="justify-center layout px-0">
                       
                        <template>
                           <v-icon
                            small
                            class="mr-2"
                            @click="crearPDF(props.item)"
                            >
                            print
                            </v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.empleado }}</td> 
                    <td>{{ props.item.digitacion }}</td>
                    <td>{{ props.item.despacho }}</td>
                    <td>{{ props.item.sobre }}</td>                  
                    <td>{{ props.item.observacion }}</td>
                    <td>{{ props.item.fecha }}</td>
                    <td>{{ props.item.usuario }}</td>
                  
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import store from '../store'
    import jsPDF from 'jspdf'
    import 'jspdf-autotable';

    export default {
        data(){
            return {
                EntregaRemesas:[],
                paginas:[], 
                modulos:[],
                empleados:[],
                editedIndex: -1,               
                dialog: false,
                search1:null,
                id_perfil:'' ,
                id_empleado:'' ,
                cantidad:0,
                digitacion:0,
                despacho:0,
                sobre:0,
                observacion:'',
                fecha:new Date().toISOString().substr(0, 10),
                headers: [
                    { text: 'Opciones', value: 'idEntregaRemesa',sortable: false },
                    { text: 'Empleado', value: 'empleado' },
                    { text: 'Digitacion', value: 'digitacion', sortable: false  }, 
                    { text: 'Despacho', value: 'despacho', sortable: false  },
                    { text: 'Sobre', value: 'sobre', sortable: false  },
                    { text: 'Observacion', value: 'Observacion', sortable: false  },  
                    { text: 'Fecha', value: 'fecha', sortable: false  }, 
                    { text: 'Usuario', value: 'usuario', sortable: false  },                

                ],
                search: ''             
            }
        },
        computed: {
             formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Rol' : 'Actualizar Rol'
            }
        },

        watch: {
        },

        created () {
            this.listar();
        },
        methods:{
            crearPDF(id){

                var doc = new jsPDF()

                    doc.text('FORMATO ENTREGA REMESAS', 14,20);
                    
                    doc.autoTable({
                            headStyles: {fillColor: 0,valign: 'middle', halign: 'center',},
                            theme: 'grid',startY: 25,
                            body: [

                                  [{content: 'Auxiliar:', colSpan: 1, rowSpan: 1, styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.empleado, styles: {valign: 'middle',halign: 'left'}}, 
                                    {content:'Fecha',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.fecha,styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Digitacion:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.digitacion, styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Despacho',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.despacho,styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Sobres:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.sobre, styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Total:',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.cantidad,styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Observacion:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.observacion, styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Usuario',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id.usuario,styles: {valign: 'middle',halign: 'left'}}]
                            ]
                        });
                        doc.setFontSize(10);
                        doc.text('FIRMA___________________________', 14, doc.autoTable.previous.finalY + 10);
                    doc.save('EntregaRemesa-'+id.idEntregaRemesa+'.pdf')

            },
             crearPDF2(id){

                var doc = new jsPDF()

                    doc.text('FORMATO ENTREGA REMESAS', 14,20);
                    
                    doc.autoTable({
                            headStyles: {fillColor: 0,valign: 'middle', halign: 'center',},
                            theme: 'grid',startY: 25,
                            body: [

                                [{content: 'Auxiliar:', colSpan: 1, rowSpan: 1, styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['empleado'], styles: {valign: 'middle',halign: 'left'}}, 
                                    {content:'Fecha',styles: {valign: 'middle',halign: 'left'}},
                                    {content: this.fecha,styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Digitacion:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['digitacion'], styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Despacho',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['despacho'],styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Sobres:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['sobre'], styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Total:',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['cantidad'],styles: {valign: 'middle',halign: 'left'}}],
                                [{content:'Observacion:', styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['observacion'], styles: {valign: 'middle',halign: 'left'}},
                                    {content:'Usuario',styles: {valign: 'middle',halign: 'left'}},
                                    {content: id[0]['usuario'],styles: {valign: 'middle',halign: 'left'}}]

                            ]
                        });
                        doc.setFontSize(10);
                        doc.text('FIRMA___________________________', 14, doc.autoTable.previous.finalY + 10);
                    doc.save('EntregaRemesa-'+id.idEntregaRemesa+'.pdf')

            },
            FiltroEmpleados(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.Empleados, 500);
                     },
            Empleados(){
                     
                        let me=this;
                        var empleadosArray=[];
                        axios.get('api/Categorias/ListarFiltroEmpleados/'+me.search1).then(function(response){
                            empleadosArray=response.data;
                            empleadosArray.map(function(x){
                                me.empleados.push({text: x.nombre_empleado,value:x.id_empleado});
                            });
                        }).catch(function(error){
                            console.log(error);
                        });
                    },
            calcular(){
                let me = this;
                me.cantidad = parseInt(me.digitacion) +parseInt(me.despacho) +parseInt(me.sobre);
            },
            editItem (item) {
    
                this.id_perfil=item.id_perfil;
                this.desc_perfil = item.desc_perfil;
                this.editedIndex=1;
                this.dialog = true
            },
            limpiar(){
                this.id_empleado='';
                this.digitacion=0;
                this.despacho=0;
                this.sobre=0;
                this.cantidad=0;
                this.observacion='';
                this.editedIndex=-1;
            },
             guardar () {

                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;

                    axios.put('api/EntregaRemesa/Crear',{

                        'id_empleado':me.id_empleado,
                        'cantidad':me.cantidad,
                        'observacion':me.observacion,
                        'id_usuario':store.state.usuario.id_usuario,
                                       
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    axios.post('api/EntregaRemesa/Crear',{

                        'id_empleado':me.id_empleado,
                        'digitacion':me.digitacion,
                        'despacho':me.despacho,
                        'sobre':me.sobre,
                        'cantidad':me.cantidad,
                        'observacion':me.observacion,
                        'id_usuario':store.state.usuario.id_usuario,

                    }).then(function(response){

                        var data=[{empleado:me.id_empleado,cantidad:me.cantidad,
                        usuario:store.state.usuario.id_usuario,observacion:me.observacion,
                        digitacion:me.digitacion,despacho:me.despacho,sobre:me.sobre}];

                        me.close();
                        me.listar();
                        me.limpiar();  
                        me.crearPDF2(data);                      
                   }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            close () {
                this.dialog = false;
                this.limpiar();
            },
            listar(){
                let me=this;
                axios.get('api/EntregaRemesa/Listar').then(function(response){
                    //console.log(response);
                    me.EntregaRemesas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
           
        }        
    }
</script>
