<script>
import axios from "axios";
export default {
  name: 'App',
  data () {
    return {
      clipped: false,
      drawer: false,
      mini:true,
      fixed: false,
      items: [
        { title: 'Daruma', icon: 'dialpad', link: 'https://transprensa.darumasoftware.com/app.php/'},
        { title: 'Nomina', icon: 'cloud_done',link:'http://transprensanw.siesacloud.com:8208/siesa/jsp/index.jsp?c=ed908e2c3f' },
        { title: 'Siesa', icon: 'cloud_upload',link:'https://clustersbs.siesacloud.com:360/' },
        { title: 'Silogtran', icon: 'local_shipping',link:'https://transprensa.colombiasoftware.net/index.php' },
        { title: 'Tics', icon: 'contact_mail' ,link:'http://192.168.93.160:8090/inventario/' },
        { title: 'GD', icon: 'attach_email' ,link:'http://192.168.93.160:8090/Gestion-documental/' }
      ],
      credenciales:{gestionhumana:false,empleados:false,informesempleados:false,permisos:false,requisiciones:false,callcenter:false,registros:false,clientes:false,
      motivos:false,extensiones:false,informescallcenter:false,administrador:false,roles:false,usuarios:false,compras:false,ingresoinventario:false,salidainventario:false,
      categorias:false,articulos:false,proveedor:false,gestiondocumental:false,correspondencia:false,remitentes:false,tipodocumento:false,},
      requisiciones:false,
      miniVariant: false,
      right: true,
      rightDrawer: false,
      informeEmpleado:false,
  
      birthdays:[],
      notifications: [
    ],
    
    }
  },
  computed:{
      logueado(){
        return this.$store.state.usuario;
    },
  },
  watch:{
    logueado(){
      this.credencial();
    }
  },
  created() {
     this.$store.dispatch("autoLogin");
     this.listar();
  },
  methods: {
    salir(){
      this.$store.dispatch("salir");
    },
    link(item){
          window.open(item, '_blank');
    },
     reload(){
         location.reload();
    },
    onClick (index) {
        this.$router.push({name:'Notifications'});
        this.$delete(this.notifications, index)
    },
    birthdayClick (index) {
        this.$router.push({name:'Birthday'});
        this.birthdays=[];
        //this.$delete(this.Birthdays, index)
    },
    credencial(){
 
      let me=this;
                if(this.logueado){

                    let header={"Authorization" : "Bearer " + this.$store.state.token};
                    let configuracion= {headers : header};
                    axios.get("api/Roles/ListaGrupos/"+ this.$store.state.usuario.rol,configuracion).then(function(response){

                      for(var $i=0;$i < response.data.length;++$i ){
                          me.credenciales[response.data[$i]["nombre"]]=true
                      }
                    
                    }).catch(function(error){
                        console.log(error);
                    });

                }else{
                  me.reload();
                  
                }
              
     },
     listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/birthday',configuracion).then(function(response){
                    //console.log(response);
                    me.birthdays=response.data;
                }).catch(function(error){
                    console.log(error);
                });
     },
  },
}
</script>
