<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Ingreso de Visitantes</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>
                    <v-btn v-if="verNuevo==0" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>

                    <v-dialog v-model="dialogCliente" max-width="1000px">
                        <v-card>
                            <v-card-title>
                                <span class="headline">{{ formTitle }}</span> 
                                                                                   
                            </v-card-title>

                            <div>
                                <v-avatar slot="activator" size="180px"  @click="onStart()" color="grey" style="margin-left:40%;margin-top:-30px" >
                                        <v-icon v-if="!img" dark size="130px" style="margin-left:27px;margin-top:-10px" >linked_camera</v-icon>
                                        <img :src="img" class="img-responsive" />
                                </v-avatar>  
                            </div>
                            
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm6 md6>
                                     <v-select v-model="tipodocumento"
                                                :items="tiposdocumentos" 
                                                label="Seleccionar tipo de documento">
                                     </v-select>
                                     <v-text-field  v-model="id_visitante" v-if="!dialogCliente"> </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="number" v-model="documento" label="Documento"  v-on:change="ValidarVisitante">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="apellido1" label="Primer Apellido">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="apellido2" label="Segundo Apellido">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="nombre1" label="Primer Nombre">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="nombre2" label="Segundo Nombre">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-select  v-model="sexo"
                                     :items="sexos" 
                                     label="Sexo">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="date" v-model="fecha_nacimiento" label="Fecha de Nacimiento">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="rh" 
                                    :items="rhs"
                                    label="RH">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="arl" 
                                    :items="arls"
                                    label="Seleccionar ARL">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="eps" 
                                    :items="epss"
                                    label="Seleccionar EPS">
                                    </v-select>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-select v-model="tipo_vehiculo" 
                                    :items="tipos_vehiculos"
                                    label="Tipo de Vehiculo">
                                    </v-select>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="placa" label="Placa">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-textarea v-model="observacion" label="Observacion">
                                    </v-textarea>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                    <v-dialog v-model="dialogFoto" max-width="1000px">
                        <v-card>
                            <v-card-title class="headline" ><code v-if="device">{{ device.label }}</code><v-spacer></v-spacer>
                               <v-btn  @click="onStop" flat icon large>
                                            <v-icon dark>close</v-icon>
                                </v-btn>
                            </v-card-title>
                            <v-card-text>
                              <vue-web-cam
                                    ref="webcam"
                                    :device-id="deviceId"
                                    width="100%"
                                    @started="onStarted"
                                    @stopped="onStopped"
                                    @error="onError"
                                    @cameras="onCameras"
                                    @camera-change="onCameraChange"
                                />
                            </v-card-text>
                            <div class="text-xs-center">
                                    <v-btn @click="onCapture" flat icon large color="indigo">
                                            <v-icon dark size="40px">camera_alt</v-icon>
                                    </v-btn>
                            </div>
                            <v-card-actions></v-card-actions>

                        </v-card>
                    </v-dialog>

                    <v-dialog v-model="dialogRetiro" max-width="290">
                        <v-card>
                            <v-card-title class="headline" >Retiro de Visitante</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span>Retirar </span>
                                visitante
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="close">
                                    Cancelar
                                </v-btn>
                                <v-btn  color="orange darken-4" flat="flat" @click="desactivar">
                                    Retirar
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
            </v-toolbar>
            
            <v-container grid-list-sm fluid class="elevation-1 white" v-if="verNuevo">
                <v-layout row wrap>
                   
                    <v-flex v-show="verNuevo == 10">
                        <v-text-field  v-model="idRegVisitante" label="IDREGISTRO">
                        </v-text-field>
                    </v-flex>
                     <v-flex xs12 sm4 md4 lg4 xl4>
                         <v-select v-model="id_visitante" 
                            :items="visitantes"
                            label="Visitante">
                        </v-select>
                    </v-flex>
                    <v-flex xs12 sm8 md8 lg8 xl8>
                        <v-btn  @click="CrearCliente()" small fab dark color="teal">
                            <v-icon dark>person_add</v-icon>
                        </v-btn>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="id_area" 
                        :items="areas"
                        :search-input.sync="search1"
                         label="Area"
                         autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 sm6 md6>
                        <v-select v-model="motivo" 
                        :items="motivos"
                        label="Motivo de visita">
                        </v-select>
                    </v-flex>

                   <v-flex xs12 >
                        <v-textarea v-model="observacion" label="Observacion">
                        </v-textarea>
                    </v-flex>

                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo()" color="blue darken-1" flat>Cancelar</v-btn>
                        <v-btn v-bind:disabled="counter" v-on:click="guardarRegistro()" color="success">Guardar</v-btn>
                    </v-flex>
                   
		        </v-layout>
            </v-container>

            <v-data-table
                :headers="headers"
                :items="Registrovisitantes"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0">

                <template slot="items" slot-scope="props"> 
                     <td class="justify-center layout px-0">
                        <template v-if="props.item.estado == 'ACTIVO'">
                            <v-icon
                            small
                            @click="metodoRetiro(props.item)"
                            >
                            check
                            </v-icon>
                        </template>
                         <template v-else>
                            <v-icon small>block</v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.id_visitante }}</td>   
                    <td>{{ props.item.nombre}}</td>                
                    <td>{{ props.item.area }}</td>  
                    <td>{{ props.item.motivo}}</td>
                    <td>{{ props.item.fecha_ingreso.split("T")[0]+" "+props.item.fecha_ingreso.split("T")[1].split(".")[0]}}</td>
                    <td v-if="props.item.estado == 'ACTIVO'">No Registra</td>
                    <td v-else>{{ props.item.fecha_salida.split("T")[0]+" "+props.item.fecha_salida.split("T")[1].split(".")[0]}}</td>
                    <td>{{ props.item.observacion}}</td>
                    <td>
                        <div v-if="props.item.estado == 'ACTIVO'">
                            <span class="blue--text">{{ props.item.estado}}</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{ props.item.estado}}</span>
                        </div>
                    </td>
                    
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>

        </v-flex>
    </v-layout>
</template>

<script>
    import axios from 'axios'
    import Swal from 'sweetalert2'
    import { WebCam } from "vue-web-cam";
    
    export default {
        components: {
             "vue-web-cam": WebCam
        },
        data(){
            return {
                Registrovisitantes:[],
                visitantes:[],
                arls:[
                      { text: "AXA COLPATRIA S.A.", value: "AXA COLPATRIA S.A." },
                      { text: "COLMENA SEGUROS", value: "COLMENA SEGUROS" },
                      { text: "COMPAÑÍA DE SEGUROS DE VIDA AURORA S.A.", value: "COMPAÑÍA DE SEGUROS DE VIDA AURORA S.A." },
                      { text: "LA EQUIDAD SEGUROS DE VIDA", value: "LA EQUIDAD SEGUROS DE VIDA" },
                      { text: "LIBERTY SEGUROS DE VIDA S.A.", value: "LIBERTY SEGUROS DE VIDA S.A." },
                      { text: "MAPFRE SEGUROS", value: "MAPFRE SEGUROS" },
                      { text: "POSITIVA", value: "POSITIVA" },
                      { text: "SEGUROS BOLÍVAR S.A.", value: "SEGUROS BOLÍVAR S.A." },
                      { text: "SEGUROS DE VIDA ALFA S.A.", value: "SEGUROS DE VIDA ALFA S.A." },
                      { text: "SURATEP SA", value: "SURATEP SA" },
                ],
                epss:[],
                tiposdocumentos:[
                    { text: "TARJETA DE IDENTIDAD", value: "TI", sortable: false },
                    { text: "CEDULA DE CIUDADANIA", value: "CC", sortable: false },
                    { text: "CEDULA EXTANJERA", value: "CE", sortable: false },
                ], 
                sexos:[
                     { text: "M", value: "M", sortable: false },
                     { text: "F", value: "F", sortable: false },
                ],
                rhs:[
                        { text: "A+", value: "A+", sortable: false },
                        { text: "A-", value: "B-", sortable: false },
                        { text: "B+", value: "B+", sortable: false },
                        { text: "B-", value: "B-", sortable: false },
                        { text: "O+", value: "O+", sortable: false },
                        { text: "O-", value: "O-", sortable: false },
                        { text: "AB+", value: "AB+", sortable: false },
                        { text: "AB-", value: "AB-", sortable: false },
                ],
                motivos:[
                        { text: "Diligencia", value: "Diligencia" },
                        { text: "Entrevista", value: "Entrevista" },
                ],
                tipos_vehiculos:[
                        { text: "MOTOCICLETA", value: "MOTOCICLETA+"},
                        { text: "CARRO", value: "CARRO" },
                        { text: "VEHICULO EMPRESARIAL", value: "VEHICULO EMPRESARIAL"},
                ],
                areas:[],
                verNuevo:0,
                editedIndex: -1,               
                dialogCliente: false,
                dialogFoto: false,
                dialogRetiro: false,
                search1: null,
                idrol:'' ,
                idRegVisitante:'',
                id_visitante:'' ,
                tipodocumento:'' ,
                documento:'' ,
                apellido1:'' ,
                apellido2:'' ,
                nombre1:'' ,
                nombre2:'' ,
                sexo:'' ,
                fecha_nacimiento:'' ,
                rh:'' ,
                arl:'',
                eps:'',
                tipo_vehiculo:'' ,
                placa:'' ,
                motivo:'' ,
                id_area:'' ,
                observacion:'' ,
                adId:'' ,
                headers: [
                    { text: 'Opciones', value: 'idRegVisitante',sortable: false },
                    { text: 'Documento', value: 'id_visitante',sortable: true},
                    { text: 'Nombre', value: 'apellido1',sortable: true },
                    { text: 'Area', value: 'area', sortable: false  },
                    { text: 'Motivo', value: 'motivo', sortable: false  },
                    { text: 'Fecha Ingreso', value: 'fecha_ingreso'},
                    { text: 'Fecha Salida', value: 'fecha_salida', sortable: false  }, 
                    { text: 'observacion', value: 'observacion', sortable: false  },
                    { text: 'estado', value: 'estado' }                     
                ],
                search: '',
                img: null,
                camera: null,
                deviceId: null,
                devices: []            
            }
        },
        computed: {
             formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Visitante' : 'Actualizar Visitante'
            },
            device: function() {
                return this.devices.find(n => n.deviceId === this.deviceId);
            }
        },
        watch: {
            camera: function(id) {
                this.deviceId = id;
            },
            devices: function() {
                // Once we have a list select the first one
                const [first, ...tail] = this.devices;
                if (first) {
                    this.camera = first.deviceId;
                    this.deviceId = first.deviceId;
                }
            }
        },
        created () {
            this.listar();
            this.selectArea()
            this.selectEps();
        },
        methods:{
            editItem (item) {
    
            this.id_visitante=item.id_visitante;
            this.tipodocumento=item.tipodocumento;
            this.documento=item.documento;
            this.apellido1=item.apellido1;
            this.apellido2=item.apellido2;
            this.nombre1=item.nombre1;
            this.nombre2=item.nombre2;
            this.sexo=item.sexo;
            this.fecha_nacimiento=item.fecha_nacimiento.split("T")[0];
            this.rh=item.rh;
            this.motivo=item.motivo;
            this.observacion=item.observacion;
            this.id_area=item.id_area;

                this.editedIndex=1;
                this.dialogCliente = true
            },
            limpiar(){
                this.tipodocumento='';
                this.documento='';
                this.apellido1='';
                this.apellido2='';
                this.nombre1='';
                this.nombre2='';
                this.sexo='';
                this.fecha_nacimiento='';
                this.rh='';
                this.motivo='';
                this.observacion='';
                this.id_area='';
                this.editedIndex=-1;
            },
            mostrarNuevo(){
                this.verNuevo=1;
                this.counter=false;
            },
            ocultarNuevo(){
                this.verNuevo=0;
                //this.verDet=0;
                //this.limpiar();
            },
            CrearCliente(){
               this.dialogCliente = true     
            },
            metodoRetiro(item){
                    this.dialogRetiro=true;
                    this.adId=item.idRegVisitante;  
            },
            guardar () {

                //Código para guardar
                let me=this;
                axios.get('api/Visitantes/ConsecutivoVisitante').then(function(response){
                let id = response.data[0]["id_visitante"];  

                    axios.post('api/Visitantes/Crear',{

                        'tipodocumento':me.tipodocumento,
                        'documento':me.documento,
                        'apellido1':me.apellido1,
                        'apellido2':me.apellido2,
                        'nombre1':me.nombre1,
                        'nombre2':me.nombre2,
                        'sexo':me.sexo,
                        'fecha_nacimiento':me.fecha_nacimiento,
                        'rh':me.rh,
                        'arl':me.arl,
                        'id_eps':me.eps,
                        'tipo_vehiculo':me.tipo_vehiculo,
                        'placa':me.placa,
                        'observacion':me.observacion,
                        'img':me.img,
                
                    }).then(function(response){

                        var Integro = parseInt(id);
                        me.visitantes.push({text: me.apellido1,value:id}); 
                        console.log(me.visitantes);
                        me.id_visitante = Integro;
                        me.close();

                   }).catch(function(error){
                        console.log(error);
                    });
                });
                
            },
            guardarRegistro () {

                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;
                    axios.put('api/RegistroVisitantes/Actualizar',{

                        'idRegVisitante':me.idRegvisitante,
                        'id_visitante':me.id_visitante,
                        'motivo':me.motivo,
                        'observacion':me.observacion,
                        'id_area':me.id_area
                                       
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    axios.post('api/RegistroVisitantes/Crear',{

                        'id_visitante':me.id_visitante,
                        'motivo':me.motivo,
                        'observacion':me.observacion,
                        'id_area':me.id_area
                    
                    }).then(function(response){
                        me.ocultarNuevo();
                        me.close();
                        me.listar();
                        me.limpiar();                        
                   }).catch(function(error){
                        console.log(error);
                    });
                    
                }
            },
            close () {
                this.dialogCliente = false;
                this.dialogRetiro= false;
                this.limpiar();
            },
            selectArea() {
                let me = this;
                var areaArray = [];
                let header = { Authorization: "Bearer " + this.$store.state.token };
                let configuracion = { headers: header };
                axios
                    .get("api/Categorias/ListarArea", configuracion)
                    .then(function(response) {
                    areaArray = response.data;
                    areaArray.map(function(x) {
                        me.areas.push({ text: x.desc_area, value: x.id_area });
                    });
                    })
                    .catch(function(error) {
                    console.log(error);
                    });
            },
            selectEps() {
                let me = this;
                var epsArray = [];
                let header = { Authorization: "Bearer " + this.$store.state.token };
                let configuracion = { headers: header };
                axios
                    .get("api/Categorias/ListarEps", configuracion)
                    .then(function(response) {
                    epsArray = response.data;
                    epsArray.map(function(x) {
                        me.epss.push({ text: x.desc_eps, value: x.id_eps });
                    });
                    })
                    .catch(function(error) {
                    console.log(error);
                    });
            },
            listar(){
                let me=this;
                axios.get('api/RegistroVisitantes/Listar').then(function(response){
                    //console.log(response);
                    me.Registrovisitantes=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            ValidarVisitante(){
                let me=this;
                 axios.get("api/Visitantes/Mostrar/" + me.documento).then(function(response){
                    Swal.fire({
                                    type: 'success',
                                    title: "El Visitante ya existe",
                                    showConfirmButton: true,
                                   
                            })
                    me.visitantes.push({text: me.documento,value:me.documento}); 
                    me.id_visitante = me.documento;
                    me.close();
                }).catch(function(error){
                    //console.log(error);
                });
            },
            desactivar(){
                let me=this;
                axios.put('api/RegistroVisitantes/Desactivar/'+me.adId,{}).then(function(response){
                    console.log(me.adId);
                    //me.adId="";
                    me.close();
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            onCapture() {
                this.img = this.$refs.webcam.capture();
                this.dialogFoto = false; 
            },
            onStarted(stream) {
                console.log("On Started Event", stream);
            },
            onStopped(stream) {
                console.log("On Stopped Event", stream);
            },
            onStop() {
                this.dialogFoto = false;   
                this.$refs.webcam.stop();
            },
            onStart() {
                this.$refs.webcam.start();
                this.dialogFoto = true   
            },
            onError(error) {
                console.log("On Error Event", error);
            },
            onCameras(cameras) {
                this.devices = cameras;
                console.log("On Cameras Event", cameras);
            },
            onCameraChange(deviceId) {
                this.deviceId = deviceId;
                this.camera = deviceId;
                console.log("On Camera Change Event", deviceId);
            }
           
        }        
    }
</script>
