<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Ingreso de Visitantes</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>

                       <v-dialog v-model="dialog" max-width="1000px">
                        <v-btn slot="activator" color="primary" dark class="mb-2">Nuevo</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm6 md6>
                                     <v-select v-model="tipodocumento"
                                                :items="tiposdocumentos" 
                                                label="Seleccionar tipo de documento">
                                     </v-select>
                                     <v-text-field  v-model="id_visitante" v-if="!dialog"> </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="number" v-model="documento" label="Documento">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="apellido1" label="Primer Apellido">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="apellido2" label="Segundo Apellido">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="nombre1" label="Primer Nombre">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="nombre2" label="Segundo Nombre">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-select  v-model="sexo"
                                     :items="sexos" 
                                     label="Sexo">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="date" v-model="fecha_nacimiento" label="Fecha de Nacimiento">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="rh" 
                                    :items="rhs"
                                    label="RH">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="arl" 
                                    :items="arls"
                                    label="Seleccionar ARL">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="eps" 
                                    :items="epss"
                                    label="Seleccionar EPS">
                                    </v-select>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-select v-model="tipo_vehiculo" 
                                    :items="tipos_vehiculos"
                                    label="Tipo de Vehiculo">
                                    </v-select>
                                </v-flex>
                                 <v-flex xs12 sm6 md6>
                                    <v-text-field type="text" v-model="placa" label="Placa">
                                    </v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-textarea v-model="observacion" label="Observacion">
                                    </v-textarea>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                    <v-dialog v-model="dialogRetiro" max-width="290">
                        <v-card>
                            <v-card-title class="headline" >Retiro de Visitante</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span>Retirar </span>
                                visitante
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="close">
                                    Cancelar
                                </v-btn>
                                <v-btn  color="orange darken-4" flat="flat" @click="desactivar">
                                    Retirar
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>


            <v-data-table
                :headers="headers"
                :items="visitantes"
                :search="search"
                class="elevation-1"
            >
                <template slot="items" slot-scope="props"> 
                     <td class="justify-center layout px-0">
                        <v-icon v-if="props.item.estado == 'ACTIVO'"
                        small
                        class="mr-2"
                        @click="editItem(props.item)"
                        >
                        edit
                        </v-icon>
                        <v-icon v-else small class="mr-2">block</v-icon>
                        <template v-if="props.item.estado == 'ACTIVO'">
                            <v-icon
                            small
                            @click="metodoRetiro(props.item)"
                            >
                            check
                            </v-icon>
                        </template>
                         <template v-else>
                            <v-icon small>block</v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.documento }}</td>                   
                    <td>{{ props.item.apellido1 +' '+ props.item.apellido2 +' '+ props.item.nombre1 +' '+ props.item.nombre2 }}</td>
                    <td>{{ props.item.tipo_vehiculo }}</td> 
                    <td>{{ props.item.placa}}</td> 
                    <td>{{ props.item.observacion}}</td>
                    <td>
                        <div v-if="props.item.estado == 'ACTIVO'">
                            <span class="blue--text">{{ props.item.estado}}</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{ props.item.estado}}</span>
                        </div>
                    </td>
                    
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                visitantes:[],
                arls:[
                      { text: "AXA COLPATRIA S.A.", value: "AXA COLPATRIA S.A." },
                      { text: "COLMENA SEGUROS", value: "COLMENA SEGUROS" },
                      { text: "COMPAÑÍA DE SEGUROS DE VIDA AURORA S.A.", value: "COMPAÑÍA DE SEGUROS DE VIDA AURORA S.A." },
                      { text: "LA EQUIDAD SEGUROS DE VIDA", value: "LA EQUIDAD SEGUROS DE VIDA" },
                      { text: "LIBERTY SEGUROS DE VIDA S.A.", value: "LIBERTY SEGUROS DE VIDA S.A." },
                      { text: "MAPFRE SEGUROS", value: "MAPFRE SEGUROS" },
                      { text: "POSITIVA", value: "POSITIVA" },
                      { text: "SEGUROS BOLÍVAR S.A.", value: "SEGUROS BOLÍVAR S.A." },
                      { text: "SEGUROS DE VIDA ALFA S.A.", value: "SEGUROS DE VIDA ALFA S.A." },
                      { text: "SURATEP SA", value: "SURATEP SA" },
                ],
                epss:[],
                tiposdocumentos:[
                    { text: "TARJETA DE IDENTIDAD", value: "TI", sortable: false },
                    { text: "CEDULA DE CIUDADANIA", value: "CC", sortable: false },
                    { text: "CEDULA EXTANJERA", value: "CE", sortable: false },
                ], 
                sexos:[
                     { text: "M", value: "M", sortable: false },
                     { text: "F", value: "F", sortable: false },
                ],
                rhs:[
                        { text: "A+", value: "A+", sortable: false },
                        { text: "A-", value: "B-", sortable: false },
                        { text: "B+", value: "B+", sortable: false },
                        { text: "B-", value: "B-", sortable: false },
                        { text: "O+", value: "O+", sortable: false },
                        { text: "O-", value: "O-", sortable: false },
                        { text: "AB+", value: "AB+", sortable: false },
                        { text: "AB-", value: "AB-", sortable: false },
                ],
                tipos_vehiculos:[
                        { text: "MOTOCICLETA", value: "MOTOCICLETA+"},
                        { text: "CARRO", value: "CARRO" },
                        { text: "VEHICULO EMPRESARIAL", value: "VEHICULO EMPRESARIAL"},
                ],
                editedIndex: -1,               
                dialog: false,
                dialogRetiro: false,
                idrol:'' ,
                id_visitante:'' ,
                tipodocumento:'' ,
                documento:'' ,
                apellido1:'' ,
                apellido2:'' ,
                nombre1:'' ,
                nombre2:'' ,
                sexo:'' ,
                fecha_nacimiento:'' ,
                rh:'' ,
                arl:'',
                eps:'',
                tipo_vehiculo:'' ,
                placa:'' ,
                observacion:'' ,
                adId:'' ,
                headers: [
                    { text: 'Opciones', value: 'id_perfil',sortable: false },
                    { text: 'Documento', value: 'documento',sortable: false },
                    { text: 'Nombre', value: 'apellido1',sortable: false },
                    { text: 'Tipo Vehiculo', value: 'tipo_vehiculo',sortable: false },
                    { text: 'Placa', value: 'placa',sortable: false },
                    { text: 'observacion', value: 'observacion', sortable: false  },
                    { text: 'estado', value: 'estado' }                     
                ],
                search: ''             
            }
        },
        computed: {
             formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Visitante' : 'Actualizar Visitante'
            }
        },

        watch: {
        },

        created () {
            this.listar();
            this.selectEps();
        },
        methods:{
            editItem (item) {
    
            this.id_visitante=item.id_visitante;
            this.tipodocumento=item.tipodocumento;
            this.documento=item.documento;
            this.apellido1=item.apellido1;
            this.apellido2=item.apellido2;
            this.nombre1=item.nombre1;
            this.nombre2=item.nombre2;
            this.sexo=item.sexo;
            this.fecha_nacimiento=item.fecha_nacimiento.split("T")[0];
            this.rh=item.rh;
            this.eps = item.id_eps;
            this.arl = item.arl;
            this.tipo_vehiculo = item.tipo_vehiculo,
            this.placa = item.placa,
            this.observacion=item.observacion;

                this.editedIndex=1;
                this.dialog = true
            },
            limpiar(){
                this.tipodocumento='';
                this.documento='';
                this.apellido1='';
                this.apellido2='';
                this.nombre1='';
                this.nombre2='';
                this.sexo='';
                this.fecha_nacimiento='';
                this.rh='';
                this.motivo='';
                this.observacion='';
                this.id_area='';
                this.editedIndex=-1;
            },
            metodoRetiro(item){
                    this.dialogRetiro=true;
                    this.adId=item.id_visitante;  
            },
            selectEps() {
                let me = this;
                var epsArray = [];
                let header = { Authorization: "Bearer " + this.$store.state.token };
                let configuracion = { headers: header };
                axios
                    .get("api/Categorias/ListarEps", configuracion)
                    .then(function(response) {
                    epsArray = response.data;
                    epsArray.map(function(x) {
                        me.epss.push({ text: x.desc_eps, value: x.id_eps });
                    });
                    })
                    .catch(function(error) {
                    console.log(error);
                    });
            },
             guardar () {

                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;
                    axios.put('api/Visitantes/Actualizar',{

                        'id_visitante':me.id_visitante,
                        'tipodocumento':me.tipodocumento,
                        'documento':me.documento,
                        'apellido1':me.apellido1,
                        'apellido2':me.apellido2,
                        'nombre1':me.nombre1,
                        'nombre2':me.nombre2,
                        'sexo':me.sexo,
                        'fecha_nacimiento':me.fecha_nacimiento,
                        'rh':me.rh,
                        'arl':me.arl,
                        'id_eps':me.eps,
                        'tipo_vehiculo':me.tipo_vehiculo,
                        'placa':me.placa,
                        'observacion':me.observacion,

                                       
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    axios.post('api/Visitantes/Crear',{

                        'tipodocumento':me.tipodocumento,
                        'documento':me.documento,
                        'apellido1':me.apellido1,
                        'apellido2':me.apellido2,
                        'nombre1':me.nombre1,
                        'nombre2':me.nombre2,
                        'sexo':me.sexo,
                        'fecha_nacimiento':me.fecha_nacimiento,
                        'rh':me.rh,
                        'arl':me.arl,
                        'id_eps':me.eps,
                        'tipo_vehiculo':me.tipo_vehiculo,
                        'placa':me.placa,
                        'observacion':me.observacion,
                
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                   }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            close () {
                this.dialog = false;
                this.dialogRetiro= false;
                this.limpiar();
            },
            listar(){
                let me=this;
                axios.get('api/Visitantes/Listar').then(function(response){
                    //console.log(response);
                    me.visitantes=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                axios.put('api/Visitantes/Desactivar/'+me.adId,{}).then(function(response){
                    console.log(me.adId);
                    //me.adId="";
                    me.close();
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
           
        }        
    }
</script>
