<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-btn>
                <download-excel
                        class = "btn"
                        :fetch   = "fetchData"
                        :fields = "json_fields"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Contactos.xls"
                        type    = "xls">
                   <v-icon>print</v-icon>
                </download-excel>
                </v-btn>
                <!--<v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>-->
                <v-toolbar-title>Contactos</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="primary" dark class="mb-2" v-if="esAdministrador">Nuevo</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md >
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <v-text-field v-model="id_contactostp" label="id"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-autocomplete v-model="id_empleado"
                                    :items="empleados" 
                                    label="Empleado"
                                    :search-input.sync="search1"
                                    v-on:keyup="FiltroEmpleados"
                                    autocomplete>
                                    </v-autocomplete>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="extension" label="Extension"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="numero" label="Numero"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="correo" label="Correo"></v-text-field>
                                </v-flex>


                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                                    </div>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==1">¿Activar Item?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==2">¿Eliminar Item?</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span v-if="adAccion==1">Activar </span>
                                <span v-if="adAccion==2">Eliminar</span>
                                el ítem {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                    Activar
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="desactivar">
                                    Eliminar
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="contactos"
                :search="search"
                class="elevation-1"
            >
                <template slot="items" slot-scope="props">
                    <td class="justify-center layout px-0" v-if="esAdministrador">
                        <v-icon
                        small
                        class="mr-2"
                        @click="editItem(props.item)"
                        >
                        edit
                        </v-icon>
                        <template v-if="props.item.estado">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(2,props.item)"
                            >
                            block
                            </v-icon>
                        </template>
                        <template v-else>
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(1,props.item)"
                            >
                            check
                            </v-icon>
                        </template>
                    </td>
                    <td v-else>
                    </td>
                    <td>{{ props.item.nombre}}</td>
                    <td>{{ props.item.cargo }}</td>
                    <td>{{ props.item.regional }}</td>
                    <td>{{ props.item.extension }}</td>
                    <td>{{ props.item.numero }}</td>
                    <td>{{ props.item.correo }}</td>
                    <td>
                        <div v-if="props.item.estado">
                            <span class="blue--text">Activo</span>
                        </div>
                        <div v-else>
                            <span class="red--text">Inactivo</span>
                        </div>
                    </td>
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import JsonExcel from 'vue-json-excel'
    import Swal from 'sweetalert2'
    import autoTable from 'jspdf-autotable';
    export default {
        data(){
            return {
                contactos:[],  
                empleados:[],              
                dialog: false,
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },
                    { text: 'Empleado', value: 'nombre' },
                    { text: 'Cargo', value: 'cargo' },
                    { text: 'Regional', value: 'regional' },
                    { text: 'Extension', value: 'extension', },
                    { text: 'Numero', value: 'numero',  },
                    { text: 'Correo', value: 'correo', },
                    { text: 'Estado', value: 'estado', sortable: false  }                
                ],
                search: '',
                search1:null,
                editedIndex: -1,
                id: '',
                id_contactostp:'',
                id_empleado:'',
                extension: '',
                numero: '',
                correo:'',
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: '',
                 json_fields: {
                    'Id':'id_contactostp',
                    'Nombre':'nombre',
                    'Cargo':'cargo',
                    'Regional':'regional',
                    'Numero':'numero',
                    'extension':'extension',
                    'Correo':'correo'

                   },            
            }
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Contacto' : 'Actualizar Contacto'
            },
            esAdministrador(){
                return this.$store.state.usuario && this.$store.state.usuario.rol =='1';
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },

        created () {
            this.listar();
        },
        methods:{
                async fetchData(){
                const response = await axios.get('api/Contactostp/Listar');
                return response.data;
                },
                startDownload(){
                   Swal.fire({

                                type: 'success',
                                title: 'Procesando datos',
                                showConfirmButton: false,
                                timer: 1000
                                })
                },
                finishDownload(){
                    //this.$swal('Descarga Exitosa !!!');
                   //const Toast = Swal.mixin({
                    //toast: true,
                    //position: 'bot-end',
                    //showConfirmButton: false,
                    //timer: 3000
                    //});

                    //Toast.fire({
                    //type: 'success',
                    //title: 'Descarga Exitosa'
                    //})
                  },
            listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Contactostp/Listar',configuracion).then(function(response){
                    //console.log(response);
                    me.contactos=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            FiltroEmpleados(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.Empleados, 500);

            },
             Empleados(){
                let me=this;
                var empleadosArray=[];
                axios.get('api/Categorias/ListarFiltroEmpleados/'+me.search1).then(function(response){
                    empleadosArray=response.data;
                    empleadosArray.map(function(x){
                        me.empleados.push({text: x.nombre_empleado,value:x.id_empleado});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            editItem (item) {
                this.id_contactostp=item.id_contactostp;
                this.empleados.push({text: item.nombre,value:item.id_empleado});
                this.id_empleado = item.id_empleado
                this.extension=item.extension;
                this.numero=item.numero;
                this.correo=item.correo;
                this.editedIndex=1;
                this.dialog = true
            },
            close () {
                this.dialog = false;
                this.limpiar();
            },
            limpiar(){
                this.id="";
                this.id_contactostp="";
                this.id_empleado="";
                this.extension="";
                this.numero="";
                this.correo="";
                this.editedIndex=-1;
            },
            guardar () {
                if (this.validar()){
                    return;
                }
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;
                    axios.put('api/Contactostp/Actualizar',{
                        'id_contactostp': me.id_contactostp,
                        'id_empleado': me.id_empleado,
                        'extension': me.extension,
                        'numero': me.numero,
                        'correo': me.correo,
                    },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                      console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    axios.post('api/Contactostp/Crear',{
                        'id_empleado': me.id_empleado,
                        'extension': me.extension,
                        'numero': me.numero,
                        'correo': me.correo,
                        'estado': 1

                    },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.correo.length<3 || this.correo.length>50){
                    this.validaMensaje.push("El correo debe tener más de 3 caracteres y menos de 50 caracteres.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.id_empleado;
                this.adId=item.id_contactostp;              
                if (accion==1){
                    this.adAccion=1;
                }
                else if (accion==2){
                    this.adAccion=2;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                axios.put('api/Contactostp/Activar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                axios.delete('api/Contactostp/Eliminar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                     
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>
