<template>
<v-container fluid grid-list-xl>
  <v-layout row wrap>
    <v-flex xs12 sm12>
      <v-card>
          <v-toolbar >
          <v-toolbar-side-icon></v-toolbar-side-icon>
          <v-toolbar-title>Cumpleaños </v-toolbar-title>
          <v-spacer>
          </v-spacer>
          {{FechaActual}}
        </v-toolbar>

        <v-list three-line>
          <template v-for="(item, index) in Birthdays">
            <v-divider v-if="index >= 1" :inset="item.inset" :key="index"></v-divider>
            <v-list-tile  :key="item.title" avatar>
              <v-list-tile-avatar>
                <v-icon>person</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title v-html="item.nombre_empleado"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.cargo"></v-list-tile-sub-title>
                <v-list-tile-sub-title v-html="item.zona"></v-list-tile-sub-title>
              </v-list-tile-content>
               <v-list-tile-action>
                <v-icon color="grey lighten-1">cake</v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-flex>


     <v-flex xs12 sm12 >
      <v-card>
         <v-toolbar>
          <v-toolbar-side-icon></v-toolbar-side-icon>
          <v-toolbar-title>Proximos Cumpleaños</v-toolbar-title>
        </v-toolbar>

        <v-list three-line>
          <template v-for="(item, index) in BirthdaysNext">
            <v-divider v-if="index >= 1" :inset="item.inset" :key="index"></v-divider>
            <v-list-tile  :key="item.title" avatar>
              <v-list-tile-avatar>
                <v-icon>person</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title v-html="item.nombre_empleado"><h1>nuevo sistema</h1></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.cargo"></v-list-tile-sub-title>
                <v-list-tile-sub-title v-html="item.zona"></v-list-tile-sub-title>
              </v-list-tile-content>
               <v-list-tile-action>
                <v-list-tile-action-text  >{{item.fecha.split("/")[0]}}-{{item.fecha.split("/")[1]}}-{{FechaActual.split("-")[0]}} </v-list-tile-action-text>
                <v-icon color="grey lighten-1">star_border</v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>


<script>
import axios from "axios";

export default {
  data() {
    return {
       Birthdays:[],  
       BirthdaysNext:[], 
       FechaActual: new Date().toISOString().substr(0, 10),
      };
  },
  created() {
    this.listar();
    this.listar2();
    this.listar3();
 
 
  },
   methods: {
      listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/birthday',configuracion).then(function(response){
                    //console.log(response);
                    me.Birthdays=response.data;

                }).catch(function(error){
                    console.log(error);
                });
            },
        listar2(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/birthdayNext',configuracion).then(function(response){
                    //console.log(response);
                    me.BirthdaysNext=response.data;

                }).catch(function(error){
                    console.log(error);
                });
            },    
          listar3(){
                let me=this;
                var birthdayArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/birthdayNextMes',configuracion).then(function(response){
                    birthdayArray=response.data;
                    birthdayArray.map(function(x){
                        me.BirthdaysNext.push(response.data);
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },  
            
             selectCargo(){
                let me=this;
                var cargoArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/ListarCargo',configuracion).then(function(response){
                    cargoArray=response.data;
                    cargoArray.map(function(x){
                        me.cargos.push({text: x.desc_cargo,value:x.id_cargo});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
     
   }
}

</script>
