<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Informe de Empleados</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
         <v-btn style="position:push-right">
                <download-excel
                        class = "btn"
                        :fetch   = "fetchDependientes"
                        :fields = "dependientes"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Dependientes.xls"
                        type    = "xls">
                    Dependientes
                </download-excel>
        </v-btn>
        <v-btn>
                <download-excel
                        class = "btn"
                        :fetch   = "fetchContactos"
                        :fields = "contactos"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Contatos.xls"
                        type    = "xls">
                    Contactos
                </download-excel>
        </v-btn>
        
        
        
         <v-btn color="primary" >
           <download-excel
                        class = "btn"
                        :fetch   = "fetchData"
                        :fields = "json_fields"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Empleados.xls"
                        type    = "xls">Descargar
            </download-excel>
        </v-btn>
      </v-toolbar>
      <v-container grid-list-sm class="pa-4 white">
        <v-layout row wrap>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_zona" :items="zonas" label="Regional" autocomplete></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
             <v-autocomplete v-model="id_cargo" :items="cargos" label="Cargo" autocomplete></v-autocomplete>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_area" :items="areas" label="Area" autocomplete></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="id_estado" :items="items" label="Retirados"></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="date" v-model="fechainicial" :items="items" label="Fecha Inicial"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="date" v-model="fechafinal" :items="items" label="Fecha Final"></v-text-field>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import JsonExcel from 'vue-json-excel'
import Swal from 'sweetalert2'
export default {
  data() {
    return {
                id_zona:100,
                id_cargo:0,
                id_area:0,
                fechainicial:"",
                fechafinal:"",
                id_estado:"N",
                items:[
                  { text: "SI", value: "S", sortable: false },
                  { text: "NO", value: "N", sortable: false },],
                zonas:[ 
                        { text: "SIN REGIONAL", value: 100, sortable: false },
                        { text: "NACIONAL", value: 0, sortable: false },
                        { text: "CALI", value: 1, sortable: false },
                        { text: "PEREIRA", value: 2, sortable: false },
                        { text: "MEDELLIN", value: 3, sortable: false },
                        { text: "PASTO", value: 4, sortable: false },
                        { text: "IBAGUE", value: 5, sortable: false },
                        { text: "NEIVA", value: 6, sortable: false },
                        { text: "POPAYAN", value: 7, sortable: false }],
                cargos:[
                   { text: "SIN CARGO", value: 0, sortable: false },
                ],
                areas:[
                  { text: "SIN AREA", value: 0, sortable: false },
                ],
                json_fields: {
                    'cedula':'id_empleado',
                    'Nombre':'nombre_empleado',
                    'Empresa':'nit_empresa',
                    'Dependencia':'id_dependencia',
                    'Cargo':'cargo',
                    'Regional':'zona',
                    'Area':'area',
                    'Municipio':'municipio',
                    'Direccion':'direccion',
                    'Barrio':'barrio',
                    'Comuna':'comuna',
                    'Estrato':'estrato',
                    'Celular':'celular',
                    'Telefono Fijo':'telefono_fijo',
                    'Corre':'correo',
                    'Fecha Nacimiento':'fec_ncto',
                    'Afp':'afp',
                    'Eps':'eps',
                    'Grupo Sanguineo':'grupo_sanguineo',
                    'Fecha Ingreso':'fec_ingreso',
                    'Estado Civil':'id_estado_civil',
                    'Nivel Academico':'id_nivelacademico',
                    'Recibe Dotacion':'recibe_dotacion',
                    'Libreta Militar':'libreta_militar',
                    'sexo':'sexo',
                    'Talla Bota':'talla_bota',
                    'Talla Camisa':'talla_camisa',
                    'Talla Pantalon':'talla_pantalon',
                    'Tipo de Conductor':'id_tipoconductor',
                    'Medio de Transporte':'medio_transporte',
                    'Placa':'placa_operativa',
                    'Jornada':'jornada',
                    'Tipo Empleado':'tipo_empleado',
                    'Conyuge':'conyuge',
                    'Id Conyuge':'cedula_conyuge',
                    'Fecha Conyuge':'fec_nctoconyuge',
                    'Nivel Academico Conyuge':'id_nivelacademico_conyuge',
                    'Labora Conyuge':'labora_conyuge',
                    'Estado':'estado',

                   },
                dependientes:{
                  'Empleado':'empleado',
                  'Tipo de Documento':'tipo_doc',
                  'Documento':'id_dependente',
                  'Regional':'regional',
                  'Nombre':'nombre',
                  'Fecha':'fecha',
                  'Parentesco':'parentesco',
                  'Sexo':'sexo',
                  'Edad':'edad',
                  'Estado':'estado',
                },
                 contactos:{
                  'Empleado':'empleado',
                  'Cedula Empleado':'id_empleado',
                  'Tipo de Contacto':'tipo_contacto',
                  'Nombre':'nombre_empleado',
                  'Celular':'celular_contacto',
                  'fijo':'fijo_contacto',
                },
      };
  },
  created() {
    this.selectArea();
    this.selectCargo();
  },
   methods: {
       async fetchData(){
          if (this.validar()) {return;}
            const response = await axios.get('api/categorias/informes/'+this.id_estado+'/'+this.id_area+'/'+this.id_cargo+'/'+this.id_zona+'/'+this.fechainicial+'/'+this.fechafinal);
                if(response.data == ""){
                     Swal.fire({

                                type: 'error',
                                title: 'No existen Registros',
                                showConfirmButton: false,
                                timer: 1000
                                })
                                        }
                return response.data;
                },
                startDownload(){
                   Swal.fire({

                                type: 'success',
                                title: 'Procesando descarga',
                                showConfirmButton: false,
                                //timer: 1000
                                })
                },
                finishDownload(){
                },
                
        async fetchDependientes(){
            const response = await axios.get('api/Categorias/ListarDependentes');
            return response.data;
                },
                startDownload(){
                   Swal.fire({
                                type: 'success',
                                title: 'Procesando descarga',
                                showConfirmButton: false,
                                timer: 1000
                                })
                },
        async fetchContactos(){
            const response = await axios.get('api/Categorias/ListarContactos');
            return response.data;
                },
                startDownload(){
                   Swal.fire({
                                type: 'success',
                                title: 'Procesando descarga',
                                showConfirmButton: false,
                                timer: 1000
                                })
                },
      validar() {

      if (!this.fechafinal) {
        Swal.fire({
                                //position: 'bot-end',
                                type: 'error',
                                title: 'Ingresar Fecha Final',
                                showConfirmButton: false,
                                timer: 1000
                                })
      }
      if (!this.fechainicial) {
       Swal.fire({
                                //position: 'bot-end',
                                type: 'error',
                                title: 'Ingresar Fecha Inicial',
                                showConfirmButton: false,
                                timer: 1000
                                })
      }
      return;
    },         
      selectArea() {
      let me = this;
      var areaArray = [];
      let header = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Categorias/ListarArea", configuracion)
        .then(function(response) {
          areaArray = response.data;
          areaArray.map(function(x) {
            me.areas.push({ text: x.desc_area, value: x.id_area });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    
    selectCargo(){
                let me=this;
                var cargoArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Categorias/ListarCargo',configuracion).then(function(response){
                    cargoArray=response.data;
                    cargoArray.map(function(x){
                        me.cargos.push({text: x.desc_cargo,value:x.id_cargo});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
   }
}

</script>