<template >
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-btn>
                <download-excel
                        class = "btn"
                        :fetch   = "fetchData"
                        :fields = "json_fields"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Registros.xls"
                        type    = "xls">
                   <v-icon>print</v-icon>
                </download-excel>
                </v-btn>
                <v-toolbar-title @click="Cargar">Registros</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-btn v-if="verNuevo==0" @click="listar()" color="primary" dark class="mb-2">Buscar</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn v-if="verNuevo==0" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>

                    <v-dialog v-model="verArticulos" max-width="1000px">
                         <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <v-text-field v-model="idcliente" label="idcliente"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field type="number" v-model="documento" label="Documento"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="nombre" label="Nombre"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="telefono" label="Telefono"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="descripcion" label="Descripción"></v-text-field>
                                </v-flex>

                                 <v-flex class="red--text" v-if="error">
                                    {{error}}
                                   <v-btn  color="blue darken-1"  flat @click="AutomaticoCliente" >
                                        Click Aquí
                                    </v-btn>
                                 </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardarCliente">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    
                    </v-dialog>

                     <v-dialog v-model="verObservacion" max-width="1000px">
                         <v-card>
                            <v-card-title>
                            <span class="headline">Ingresar la Observación</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>

                                   <v-flex  md12>
                                    <v-textarea
                                        v-model="observacion"
                                        label="Observación"
                                        hint="Hint text"
                                        ></v-textarea>
                                    </v-flex>
                                 
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click="agregarObservacion(observacion)">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    
                    </v-dialog>
                 
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==1">¿Activar Item?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==2">¿Anular Ingreso?</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span v-if="adAccion==1">Activar </span>
                                <span v-if="adAccion==2">Anular </span>
                                el ítem {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                    Activar
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="desactivar">
                                    Anular
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="registros"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template slot="items" slot-scope="props">
                    <td class="justify-center layout px-0">
                        <v-icon v-if="props.item.estado=='CUMPLIDO'"
                        small
                        class="mr-2"
                        @click="verDetalles(props.item)"
                        >
                        tab
                        </v-icon>
                        <v-icon v-else
                        small
                        class="mr-2"
                        @click="verDetalles(props.item)"
                        >
                        create
                        </v-icon>
            
                    </td>
                    <td>{{ props.item.idregistro }}</td>
                    <td>{{ props.item.municipio}}</td>
                    <td>{{ props.item.documento}}</td>
                    <td>{{ props.item.cliente }}</td>
                    <td>{{ props.item.telefono}}</td>
                    <td>{{ props.item.motivo }}</td>
                    <td><a v-bind:href="'https://transprensa.colombiasoftware.net/index.php?page=Despacho.Remesa_013.Home_007&remesa_codigo=' + props.item.remesa + '#no-back-button'"  target="_blank">{{ props.item.remesa }}</a></td>
                    <td><a v-bind:href="'https://transprensa.colombiasoftware.net/index.php?page=Despacho.Ordencargue.Home_007&ordcar_codigo=' + props.item.ordencargue + '#no-back-button'"  target="_blank">{{ props.item.ordencargue }}</a></td>
                    <td>{{ props.item.usuario }}</td>
                    <td>{{ props.item.fecha.split("T")[0]}}</td>
                    <td>{{ props.item.fecha.split("T")[1].split(".")[0]}}</td>
                    <td>
                        <div v-if="props.item.estado =='CUMPLIDO'">
                            <span class="blue--text">{{props.item.estado}}</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{props.item.estado}}</span>
                        </div>
                    </td>
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>

            <v-container grid-list-sm class="elevation-1 white" v-if="verNuevo" fluid>
                <v-layout row wrap>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="idcliente"
                        :items="clientes" 
                        label="Documento Cliente"
                        :search-input.sync="search3"
                        v-on:change="NombreCliente"
                        v-on:keyup="FiltroDocumento"
                        autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex v-show="verNuevo == 10">
                        <v-text-field  v-model="idregistro" label="IDREGISTRO">
                        </v-text-field>
                    </v-flex>
                     <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="nombrecliente"
                        :items="Nclientes" 
                        label="Nombre cliente"
                        :search-input.sync="search1"
                        v-on:change="IdCliente"
                        v-on:keyup="FiltroCliente"
                        data-id="1010"
                        autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 sm2 md2 lg2 xl2>
                        <v-btn v-if="verDet!==1" @click="mostrarArticulos()" small fab dark color="teal">
                            <v-icon dark>list</v-icon>
                        </v-btn>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="idmunicipio" 
                        :items="municipios"
                        :search-input.sync="search2"
                         v-on:keyup="FiltroMunicipio"
                         label="Municipio"
                         autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field v-model="remesa" label="Remesa">
                        </v-text-field>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="idmotivo" 
                        :items="motivos"
                         label="Motivo"
                         autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field type="number" v-model="ordencargue" label="Orden de cargue">
                        </v-text-field>
                    </v-flex>

                     <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-select v-model="transfiere"
                        :items="transferir"
                         label="Transfiere">
                        </v-select>
                    </v-flex>

                     <v-flex xs12 sm4 md4 lg4 xl4 >
                        <v-autocomplete v-model="idextencion" 
                        :items="extensiones"
                         label="Ext"
                         autocomplete>
                        </v-autocomplete>
                    </v-flex>

                      <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="estado" 
                        :items="estados"
                         label="Estado"
                         autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    
                     <v-flex xs12 sm2 md2 lg2 xl2>
                        <v-btn  v-if="verDet!==1" @click="mostrarObservacion()" small fab dark color="teal">
                            <v-icon dark>create</v-icon>
                        </v-btn>
                    </v-flex>

                
                    
                    <v-flex xs12 sm2 md2 lg2 xl2 v-if="errorArticulo">
                        <div class="red--text" v-text="errorArticulo">
                        </div>
                    </v-flex>

                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-data-table  
                                    :headers="cabeceraObservaciones"
                                    :items="observaciones"
                                    hide-actions
                                    class="elevation-1"
                                >
                                    <template slot="items" slot-scope="props">
                                        <td style = "width:70%;">{{ props.item.observacion }}</td>
                                        <td style = "width:250px;">{{ props.item.usuario }}</td>
                                        <td v-if="verDet!==0" style = "width:130px;">{{ props.item.fecha.split("T")[0]}}</td>
                                        <td v-if="verDet!==0">{{ props.item.fecha.split("T")[1].split(".")[0]}}</td>
                                        
                                    </template>
                                    <template slot="no-data">
                                        <h3>No hay observaciones agregadas.</h3>
                                    </template>
                                </v-data-table>
                              &nbsp;
                        </v-flex>
                        <v-flex>
                        </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                        </div>
                    </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo()" color="blue darken-1" flat>Cancelar</v-btn>
                        <v-btn v-if="verDet==0"  v-bind:disabled="counter" v-on:click="guardar()" color="success">Guardar</v-btn>
                        <v-btn v-if="verDet==2"  @click.once="actualizar()" color="success">Actualizar</v-btn>
                    </v-flex>
                   
		        </v-layout>
            </v-container>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import Vue from 'vue'
    import JsonExcel from 'vue-json-excel'
    import VueSweetalert2 from 'vue-sweetalert2'
    import Swal from 'sweetalert2'
    Vue.use(VueSweetalert2, options)
    const options = {
                            confirmButtonColor: '#41b882',
                            cancelButtonColor: '#ff7674'
                            }

    Vue.component('downloadExcel', JsonExcel)
           
   

    export default {
        data(){
            return {
                registros:[],                
                dialog: false,
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },
                    { text: 'Codigo', value: 'idregistro'},
                    { text: 'Municipio', value: 'municipio',sortable: false },
                    { text: 'Doc Cliente', value: 'documento',sortable: false },
                    { text: 'cliente', value: 'cliente', sortable: false  },
                    { text: 'Telefono', value: 'telefono', sortable: false  },
                    { text: 'Motivo', value: 'motivo', sortable: false  },
                    { text: 'Remesa', value: 'remesa', sortable: false  },
                    { text: 'Orden de Cargue', value: 'ordencargue', sortable: false  },
                    { text: 'Usuario', value: 'usuario',sortable: false},
                    { text: 'Fecha', value: 'fecha',sortable: false},
                    { text: 'Hora', value: 'fecha',sortable: false},
                    { text: 'Estado', value: 'estado'  }                
                ],
                cabeceraObservaciones: [
                    { text: 'Observacion', value: 'observacion', sortable: false },
                    { text: 'Usuario', value: 'usuario',sortable: false},  
                    { text: 'Fecha', value: 'fecha',sortable: false},
                    { text: 'Hora', value: 'fecha',sortable: false},     
                          
                ],
                observaciones:[ 
                ],
                search: '',
                id: '',
                idregistro:'',
                clientes:[                   
                ],
                Nclientes:[                   
                ],
                idcliente:'',
                nombrecliente:'',
                nombre:'',
                telefono:'',
                documento:'',
                descripcion:'',
                municipios:[],
                idmunicipio:'',
                remesa:'0',
                idmotivo:'',
                motivos:[],
                ordencargue:'0',
                transferir:[
                    {  text: 'SI',value: 'SI', sortable: false },
                    {  text: 'NO',value: 'NO', sortable: false }            
                ],
                transfiere:'NO',
                estados:[
                    {  text: 'PENDIENTE',value: 'PENDIENTE', sortable: false },
                    {  text: 'CUMPLIDO',value: 'CUMPLIDO', sortable: false }            
                ],
                estado:'', 
                idextencion:1,
                extensiones:[],
                observacion:'',
                verNuevo:0,
                search1: null,
                search2: null,
                search3: null,
                error:null,
                errorArticulo:null,
                totalParcial:0,
                totalImpuesto:0,
                total:0,
                texto:'',
                verArticulos:0,
                verObservacion:0,
                verDet: 0,
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: '' ,
                controladorTiempo:'', 
                counter:false,
                json_fields: {
                    'Registro': 'idregistro',
                    'Municipio': 'municipio',
                    'Cliente': 'cliente',
                    'Motivo': 'motivo',
                    'Extension': 'extension',
                    'Municipio': 'municipio',
                    'Remesa': 'remesa',
                    'Oden de Cargue': 'ordencargue',
                    'Observacion': 'observacion',
                    'Usuario': 'usuario',
                    'Fecha': 'fecha',
                    'Hora': 'hora',
                    'Estado': 'estado',
                    
                },
                json_meta: [
            [
                {
                    'key': 'charset',
                    'value': 'utf-8'
                }
            ]
        ],          
            }
        },
        computed: {
            calcularTotal:function(){
                var resultado=0.0;
                for(var i=0;i<this.detalles.length;i++){
                    resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad);
                }
                return resultado;
            },
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Cliente' : 'Nuevo Cliente'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            var me = this;
            this.listar();
            this.selectMotivo();
            this.selectExtension();
            //setInterval(function() {me.listar();} , 15000);
        },
        methods:{
                
                Cargar(){
                this.listar();
                console.log("se recargo");
                },
                async fetchData(){
                const response = await axios.get('api/registrocall/informe');
                return response.data;
                },
                startDownload(){
                   Swal.fire({
                                type: 'success',
                                title: 'Procesando datos',
                                showConfirmButton: false,
                                //timer: 1000
                                })
                },
                finishDownload(){
                },
            NombreCliente(){
                 let me=this;
                 me.nombrecliente = me.idcliente;
            },
            IdCliente(){
                 let me=this;
                 me.idcliente = me.nombrecliente;
            },
            mostrarNuevo(){
                this.verNuevo=1;
                this.counter=false;
            },
            ocultarNuevo(){
                this.verNuevo=0;
                this.verDet=0;
                this.limpiar();
            },
            mostrarArticulos(){
                this.verArticulos=1;
                //this.limpiarcliente();
            },
            mostrarObservacion(){
                this.verObservacion=1;
            },
            ocultarArticulos(){
                this.verArticulos=0;
            },
            ocultarObservacion(){
                this.verObservacion=0;
            },
             close () {
                this.verArticulos = false;
                this.verObservacion = false;
                //this.select();
            },
            agregarObservacion(data){

                    if(this.idregistro !== "" & this.estado == "PENDIENTE"){
                            let me=this;
                            axios.post('api/RegistroCall/CrearObservacion',{
                                'idregistro':me.idregistro,
                                'observacion':me.observacion,
                                'usuario': me.$store.state.usuario.nombre,
                            }).then(function(response){
                                me.listarObservacion(me.idregistro);
                                me.verObservacion = false;
                                me.observacion = ""
                            }).catch(function(error){
                                console.log(error);
                            });
                    }else{
                            var fecha= new Date();
                            this.observaciones.push({ observacion:this.observacion,usuario:this.$store.state.usuario.nombre});
                            this.verObservacion = false;
                            this.observacion = ""
                    }
            },
            listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                let url='';
                if (!me.search){
                    url='api/RegistroCall/Listar';
                }
                else{
                    url='api/RegistroCall/ListarFiltro/'+me.search;
                }
                axios.get(url,configuracion).then(function(response){
                    //console.log(response);
                    me.registros=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
             listarfiltro(){
                let me=this;
                if(store.state.usuario.rol == 1){
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Permisos/ListarFiltro/'+me.search,configuracion).then(function(response){
                    //console.log(response);
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });
              }
            },
            FiltroCliente(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.select, 500);
            },
            select(){
                let me=this;
                var clientesArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                //axios.get('api/ClientesCall/Select',configuracion).then(function(response){
                axios.get('api/ClientesCall/ListarFiltro/'+me.search1,configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.documento,value:x.idcliente});
                        me.Nclientes.push({text: x.nombre,value:x.idcliente});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             FiltroDocumento(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.selectDocumento, 500);
            },
            selectDocumento(){
                let me=this;
                var clientesArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                console.log(me.search3);
                //axios.get('api/ClientesCall/Select',configuracion).then(function(response){
                axios.get('api/ClientesCall/Listardocumento/'+me.search3,configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.documento,value:x.idcliente});
                        me.Nclientes.push({text: x.nombre,value:x.idcliente});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            FiltroMunicipio(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.selectMunicipio, 500);
            },
            selectMunicipio(){
                let me=this;
                var municipiosArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                //axios.get('api/Categorias/ListarMunicipio',configuracion).then(function(response){
                axios.get('api/Categorias/ListarFiltro/'+me.search2,configuracion).then(function(response){
                    municipiosArray=response.data;
                    municipiosArray.map(function(x){
                        me.municipios.push({text: x.concat_municipio,value:x.id_municipio});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectMotivo(){
                let me=this;
                var motivoArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/MotivosCall/Select',configuracion).then(function(response){
                    motivoArray=response.data;
                    motivoArray.map(function(x){
                        me.motivos.push({text: x.nombre,value:x.idmotivo});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             selectExtension(){
                let me=this;
                var ExtensionArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/ExtensionCall/Select',configuracion).then(function(response){
                    ExtensionArray=response.data;
                    ExtensionArray.map(function(x){
                        me.extensiones.push({text: x.nombre,value:x.idextencion});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             listarObservacion(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/RegistroCall/ListarObservaciones/'+id,configuracion).then(function(response){
                    //console.log(response);
                    me.observaciones=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            verDetalles(item){
                this.limpiar();
                this.idregistro=item.idregistro;
                this.clientes.push({text: item.documento,value:item.idcliente});
                this.Nclientes.push({text: item.nombrecliente,value:item.idcliente});
                this.idcliente=item.idcliente;
                this.nombrecliente=item.idcliente;
                this.municipios.push({text: item.municipio,value:item.id_municipio});
                this.idmunicipio=item.id_municipio;
                this.remesa=item.remesa;
                this.ordencargue=item.ordencargue;
                this.transfiere=item.transfiere;
                this.idextencion=item.idextension;
                this.idmotivo=item.idmotivo;
                this.observacion=item.observacion;
                this.estado=item.estado;
                this.listarObservacion(item.idregistro);
                this.verNuevo=1;
                if(item.estado !== "CUMPLIDO"){
                this.verDet=2;
                }else{
                this.verDet=1;
                }
            },
            limpiar(){
                this.id="";
                this.idregistro="";
                this.idcliente="";
                this.idmunicipio="";
                this.remesa="0";
                this.ordencargue="0";
                this.transfiere="NO";
                this.idextencion=1;
                this.observacion="";
                this.idmotivo="";
                this.estado="";
                this.nombrecliente="";
                this.observaciones=[];
                this.municipios=[];
                this.clientes=[];
                this.Nclientes=[];
            },
             limpiarcliente(){
                this.idcliente="";
                this.telefono="";
                this.nombre="";
                this.documento="";
                this.descripcion="";
                this.error=null;
            },
            guardar () {
                this.counter=true;
                if (this.validar()){
                this.counter=false;
                    return;
                }
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};                
                let me=this;
                axios.post('api/RegistroCall/Crear',{

                    'idcliente': me.idcliente,
                    'id_municipio': me.idmunicipio,
                    'remesa':me.remesa,
                    'ordencargue': me.ordencargue,
                    'transfiere':me.transfiere,
                    'idextencion':me.idextencion,
                    'idmotivo':me.idmotivo,
                    //'observacion':me.observacion,
                    'estado':me.estado,
                    'id_usuario':me.$store.state.usuario.id_usuario,
                    'observaciones':me.observaciones
                      
                },configuracion).then(function(response){
                    Swal.fire({
                                position: 'bot-end',
                                type: 'success',
                                title: 'Registro Guardado',
                                showConfirmButton: false,
                                timer: 1000
                                })
                    me.ocultarNuevo();
                    me.listar();
                    me.limpiar();                      
                }).catch(function(error){
                    console.log(error);
                });
            },
             actualizar () {
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};                
                let me=this;
                axios.put('api/RegistroCall/Actualizar',{
                    'idregistro':me.idregistro,
                    'estado':me.estado
                },configuracion)
                .then(function(response){
                    me.ocultarNuevo();
                    me.listar();
                    me.limpiar();                        
                }).catch(function(error){
                    console.log(error);

                });
            },
             guardarCliente () {

                let me=this;
                axios.get('api/ClientesCall/ConsecutivoCliente').then(function(response){
                    let id = response.data[0]["idcliente"];      

                    //Código para guardar
                    axios.post('api/ClientesCall/Crear',{
                        'nombre': me.nombre,
                        'documento':me.documento,
                        'telefono':me.telefono,
                        'descripcion': me.descripcion,
                    },).then(function(response){
                        me.close();
                        var Integro = parseInt(id);
                        me.clientes.push({text: me.documento,value:id});
                        me.Nclientes.push({text: me.nombre,value:id}); 
                        me.nombrecliente=Integro; 
                        me.limpiarcliente();                      
                    }).catch(err => {
                        if (err.response){
                            me.error=err.response.data;
                        }else{
                            me.error="Ocurrió un error";
                        }
                        //console.log(err)
                    })
                });   
            },
            AutomaticoCliente(item){
                this.verArticulos=0;
                var documento = this.documento;
                var Integro = parseInt(documento);
                  this.idcliente=Integro;0.
            },
            validar(){  
                this.valida=0;
                this.validaMensaje=[];

                if (!this.idmunicipio){
                    this.validaMensaje.push("Seleccione un Municipio.");
                }
                if (!this.idmotivo){
                    this.validaMensaje.push("Seleccione un Motivo.");
                }
                if (!this.idcliente){
                    this.validaMensaje.push("Ingrese el cliente.");
                }
                if (!this.ordencargue){
                    this.validaMensaje.push("Ingrese Orden Cargue.");
                }
                if (!this.transfiere){
                    this.validaMensaje.push("Seleccionar Una Opcion en Tranfiere.");
                }
                if (!this.estado){
                    this.validaMensaje.push("Seleccionar Un Estado.");
                }
                if (this.observaciones == ""){
                    this.validaMensaje.push("Ingresar Observación.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.num_comprobante;
                this.adId=item.idingreso;                
                if (accion==1){
                    this.adAccion=1;
                }
                else if (accion==2){
                    this.adAccion=2;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            /*
            activar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Usuarios/Activar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            */
            desactivar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Ingresos/Anular/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }

</script>
