<template >
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-btn v-if="administrador==true">
                <download-excel
                        class = "btn"
                        :fetch   = "fetchData"
                        :fields = "json_fields"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Correspondencia.xls"
                        type    = "xls">
                   <v-icon>print</v-icon>
                </download-excel>
                </v-btn>
                <v-toolbar-title @click="Cargar">Correspondencia</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-btn v-if="verNuevo==0" @click="listar()" color="primary" dark class="mb-2">Buscar</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn v-if="verNuevo==0 && administrador==true" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>

                    <v-dialog v-model="verArticulos" max-width="500px">
                         <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <v-text-field v-model="id_remitente" label="idmotivo"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12>
                                    <v-text-field  v-model="documento" label="Documento"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 >
                                    <v-text-field v-model="nombre" label="Nombre"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12 >
                                    <v-text-field v-model="direccion" label="Direccion"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12 >
                                    <v-text-field v-model="ciudad" label="Ciudad"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 >
                                    <v-text-field v-model="telefono" label="Telefono"></v-text-field>
                                </v-flex>
                                 <v-flex xs12 sm12 md12 >
                                    <v-text-field v-model="email" label="Correo"></v-text-field>
                                </v-flex>

                               

                                 <v-flex class="red--text" v-if="error">
                                    {{error}}
                                   <v-btn  color="blue darken-1"  flat @click="AutomaticoCliente" >
                                        Click Aquí
                                    </v-btn>
                                 </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardarCliente">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    
                    </v-dialog>

                     <v-dialog v-model="verObservacion" max-width="1000px">
                         <v-card>
                            <v-card-title>
                            <span class="headline">Ingresar la Observación</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>

                                   <v-flex  md12>
                                    <v-textarea
                                        v-model="observacion"
                                        label="Observación"
                                        hint="Hint text"
                                        ></v-textarea>
                                    </v-flex>
                                 
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click="agregarObservacion(observacion)">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    
                    </v-dialog>
                 
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==1">¿Activar Item?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==2">¿Desea Cumplir?</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span v-if="adAccion==1">Activar </span>
                                <span v-if="adAccion==2">Cumplir </span>
                                el ítem {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                    Activar
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="cumplir">
                                   Cumplir
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="registros"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template slot="items" slot-scope="props">
                    <td class="justify-center layout px-0">

                        <v-icon  v-if="RolSeguridad" small class="mr-2">
                                block
                        </v-icon>
                         <v-icon v-else-if="props.item.estado=='CUMPLIDO'"
                                small class="mr-2" @click="verDetalles(props.item)">
                                tab
                        </v-icon>
                        <v-icon v-else small class="mr-2" @click="verDetalles(props.item)">
                                create
                        </v-icon>
                        <v-icon v-if="props.item.estado=='CUMPLIDO' || props.item.estado=='CERRADO' || RolSeguridad"  small>
                                block
                        </v-icon>
                         <v-icon v-else
                                    small @click="activarDesactivarMostrar(2,props.item)">
                                    check
                        </v-icon>
            
                    </td>
                    <td>{{ props.item.id_correspondencia}}</td>
                    <td>{{ props.item.tipodocumento}}</td>
                    <td>{{ props.item.remitente }}</td>
                    <td>{{ props.item.destinatario}}</td>
                    <td>{{ props.item.fecha_ingreso.split("T")[0]}}</td>
                    <td>{{ props.item.fecha_cumplido.split("T")[1].split(".")[0]}}</td>
                    <td>
                        <div v-if="props.item.estado =='CUMPLIDO'">
                            <span class="blue--text">{{props.item.estado}}</span>
                        </div>
                         <div v-else-if="props.item.estado =='CERRADO'">
                            <span class="green--text">{{props.item.estado}}</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{props.item.estado}}</span>
                        </div>
                    </td>
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>

            <v-container grid-list-sm class="elevation-1 white" v-if="verNuevo" fluid>
                <v-layout row wrap>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="idcliente"
                        :items="clientes" 
                        label="Documento Remitente"
                        :search-input.sync="search3"
                        v-on:keyup="FiltroDocumento"
                        autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex v-show="verNuevo == 10">
                        <v-text-field  v-model="id_correspondencia" label="Correspondencia">
                        </v-text-field>
                    </v-flex>
                     <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="nombrecliente"
                        :items="Nclientes" 
                        label="Nombre Remitente"
                        :search-input.sync="search1"
                        v-on:change="IdCliente"
                        v-on:keyup="FiltroCliente"
                        data-id="1010"
                        autocomplete>
                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 sm2 md2 lg2 xl2>
                        <v-btn v-if="verDet!==1" @click="mostrarArticulos()" small fab dark color="teal">
                            <v-icon dark>list</v-icon>
                        </v-btn>
                    </v-flex>
                   
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="idmotivo" 
                        :items="motivos"
                         label="Tipo de Documento"
                         v-on:change="TipoDocumento()"
                         autocomplete
                         >
                        </v-autocomplete>
                    </v-flex>

                    <v-flex xs4 sm4 md4 >
                        <v-text-field v-model="desdoc" label="Descripcion Tipo Documento"></v-text-field>
                    </v-flex>

                     <v-flex xs4 sm4 md4 >
                        <v-text-field type="number" v-model="folios" label="Cantidad de Folios"></v-text-field>
                    </v-flex>

                    <v-flex xs4 sm4 md4 >
                        <v-text-field type="text" v-model="anexos" label="Anexos"></v-text-field>
                    </v-flex>

                    <v-flex xs4 sm4 md4>
                        <v-select 
                            :items="prioridades"
                            v-model="prioridad"
                            label="Prioridad"
                            single-line
                            readonly>
                        </v-select>          
                    </v-flex>

                    <v-flex>
                        <v-text-field  v-model="fecha" label="Fecha Plazo" type="date">
                        </v-text-field>
                    </v-flex>

                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-autocomplete v-model="id_usuario" 
                            :items="id_usuarios"
                            label="Destinatario"
                            v-on:change="AsgDest"
                            autocomplete>
                        </v-autocomplete>
                    </v-flex>

                     <v-flex xs4 sm4 md4 >
                        <v-text-field v-model="DesArea" label="Destinatario Area" readonly></v-text-field>
                    </v-flex>

                     <v-flex xs4 sm4 md4 >
                        <v-text-field v-model="DesCargo" label="Destinatario Cargo" readonly></v-text-field>
                    </v-flex>

                     <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field  v-model="estado" label="Estado" type="text" readonly>
                        </v-text-field>
                    </v-flex>

                    <v-flex xs4 sm4 md4 >
                         <input type="file" @change="onFileChange"></input>
                    </v-flex>

                    <v-flex  md12>
                        <v-textarea
                            v-model="observacion"
                            label="Observación"
                            hint="Hint text"
                            ></v-textarea>
                    </v-flex>
        
                    <v-flex xs12 sm2 md2 lg2 xl2 v-if="errorArticulo">
                        <div class="red--text" v-text="errorArticulo">
                        </div>
                    </v-flex>

                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-data-table  
                                    :headers="cabeceraObservaciones"
                                    :items="observaciones"
                                    :sort-by.sync="sortBy"
                                    :sort-desc.sync="sortDesc"
                                    hide-actions
                                    class="elevation-1"
                                >
                                    <template slot="items" slot-scope="props">
                                        <td style = "width:30%;">{{ props.item.observacion }}</td>
                                        <td >{{ props.item.cambio_estado }}</td>
                                        <td style = "width:250px;">{{ props.item.usuario }}</td>
                                        <td v-if="verDet!==0" style = "width:130px;">{{ props.item.fecha.split("T")[0]}}</td>
                                        <td v-if="verDet!==0">{{ props.item.fecha.split("T")[1].split(".")[0]}}</td>
                                        
                                    </template>
                                    <template slot="no-data">
                                        <h3>No hay observaciones agregadas.</h3>
                                    </template>
                                </v-data-table>
                              &nbsp;
                        </v-flex>
                        <v-flex>
                        </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                        </div>
                    </v-flex>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo()" color="blue darken-1" flat>Cancelar</v-btn>
                        <v-btn v-if="verDet==0"  v-bind:disabled="counter" v-on:click="guardar()" color="success">Guardar</v-btn>
                        <v-btn v-if="verDet==1"  @click="cerrarTramite()" color="success">Cerrar Tramite</v-btn>
                        <v-btn v-if="verDet==2"  @click.prevent="actualizar()" color="success">Actualizar</v-btn>
                    </v-flex>
                   
		        </v-layout>
            </v-container>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import Vue from 'vue'
    import store from '../store'
    import JsonExcel from 'vue-json-excel'
    import VueSweetalert2 from 'vue-sweetalert2'
    import Swal from 'sweetalert2'
    import emailjs from 'emailjs-com';

    Vue.use(VueSweetalert2, options)
    const options = {
                            confirmButtonColor: '#41b882',
                            cancelButtonColor: '#ff7674'
                            }

    Vue.component('downloadExcel', JsonExcel)
           
   

    export default {
        data(){
            return {
                textoDocumento:'',
                registros:[],                
                dialog: false,
                administrador:false,
                RolSeguridad:false,
                sortBy: 'fat',
                sortDesc: true,
                headers: [
                    { text: 'Opciones', value: '' },
                    { text: 'Numero Radicado', value: 'id_correspondencia' },
                    { text: 'Tipo de Documento', value: 'tipodocumento',sortable: false },
                    { text: 'Remitente', value: 'remitente', sortable: false  },
                    { text: 'Destinatario', value: 'destinatario',sortable: false},
                    { text: 'Fecha', value: 'fecha',sortable: false },
                    { text: 'Hora', value: 'fecha',sortable: false},
                    { text: 'Estado', value: 'estado' }                
                ],
                cabeceraObservaciones: [
                    { text: 'Descripción de Registro', value: 'observacion', sortable: false },
                    { text: 'Estado', value: 'cambio_estado',sortable: false},
                    { text: 'Usuario', value: 'usuario',sortable: false},  
                    { text: 'Fecha', value: 'fecha',sortable: false},
                    { text: 'Hora', value: 'fecha',sortable:true},     
                          
                ],
                fecha: new Date().toISOString().substr(0, 10),
                observaciones:[ 
                ],
                prioridad:'',
                prioridades: [
                    { text: 'ALTA', value: 'ALTA' },
                    { text: 'MEDIA', value: 'MEDIA'},  
                    { text: 'BAJA', value: 'BAJA'},      
                ],
                search: '',
                id: '',
                id_correspondencia:'',
                clientes:[                   
                ],
                Nclientes:[                   
                ],
                id_usuario:'',
                id_usuarios:[],
                nombrecliente:'',
                idcliente:'',
                id_remitente:'',
                documento:'',
                nombre:'',
                telefono:'',
                email:'',
                direccion:'',
                ciudad:'',
                correo: '' ,
                idmotivo:4,
                motivos:[],
                tipoDocumentos:[],
                desdoc:'',
                anexos:'',
                folios:'',
                DesArea:[],
                DesCargo:[],
                file:'',
                observacion:'',
                estado:'',
                verNuevo:0,
                search1: null,
                search3: null,
                error:null,
                errorArticulo:null,
                totalParcial:0,
                totalImpuesto:0,
                total:0,
                texto:'',
                verArticulos:0,
                verObservacion:0,
                verDet: 0,
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: '' ,
                controladorTiempo:'', 
                counter:false,
                json_fields: {
                    'Numero Radicado':'id_correspondencia',
                    'Remitente':'remitente',
                    'Destinatario':'destinatario',
                    'Tipo Documento':'tipodocumento',
                    'Descripcion Documento':'desdoc',
                    'Prioridad':'prioridad',
                    'Plazo':'plazo',
                    'Usuario':'usuario',
                    'Fecha Ingreso':'fecha_ingreso',
                    'Fecha Cumplido':'fecha_cumplido',
                    'Folios':'folios',
                    'Anexos':'anexos',
                    'Estado':'estado',
                    
                },
                json_meta: [
            [
                {
                    'key': 'charset',
                    'value': 'utf-8'
                }
            ]
        ],          
            }
        },
        computed: {
            calcularTotal:function(){
                var resultado=0.0;
                for(var i=0;i<this.detalles.length;i++){
                    resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad);
                }
                return resultado;
            },
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Remitente' : 'Nuevo Remitente'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            var me = this;
            this.listar();
            this.selectMotivo();
            this.selectUsuarios();
            //setInterval(function() {me.listar();} , 15000);
        },
        methods:{
            AsgDest(){

                 this.id_usuarios.find(file => {
                    if (file.value === this.id_usuario) {
                        this.DesArea = file.regional;
                        this.DesCargo = file.cargo;
                    }
                })
                
            },  
            Cargar(){
                this.listar();
                console.log("se recargo");
                },
                async fetchData(){
                const response = await axios.get('api/Correspondencia/Informe');
                return response.data;
                },
                startDownload(){
                   Swal.fire({
                                type: 'success',
                                title: 'Procesando datos',
                                showConfirmButton: false,
                                //timer: 1000
                                })
                },
                finishDownload(){
            },
            sendEmail: function (){
                var usuario = "";
                var tipoDocumento ="";
                this.motivos.find(file => {
                    if (file.value === this.idmotivo) {
                        tipoDocumento = file.text;
                    }
                })
                this.id_usuarios.find(file => {
                    if (file.value === this.id_usuario) {
                        usuario=file.text;
                    }
                })
                let data = {
                            //from_name: "prueba",
                            //from_email: "prueba",
                            tittle:"Tienes Correspondencia pendiente por recibir",
                            to_name: usuario,
                            to_email:this.correo,
                            message: this.observacion,
                            subject: "Ingreso de",
                            prioridad:this.prioridad,
                            tipo_documento:tipoDocumento,
                            fecha:this.fecha
                            };
                emailjs.send('gmail', 'template_baWsJSDt', data, 'user_GRmUlv1wMz7EhyBfH7E1g')
                    .then((result) => {
                        console.log('SUCCESS!', result.status, result.text);
                    }, (error) => {
                        console.log('FAILED...', error);
                    });
            },
            onFileChange(event){
               this.file = event.target.files[0]
               console.log(this.file)
            },
            NombreCliente(){
                 let me=this;
                 me.nombrecliente = me.idcliente;
            },
            IdCliente(){
                 let me=this;
                 me.idcliente = me.nombrecliente;
            },
            mostrarNuevo(){
                this.verNuevo=1;
                this.counter=false;
            },
            ocultarNuevo(){
                this.verNuevo=0;
                this.verDet=0;
                this.limpiar();
            },
            mostrarArticulos(){
                this.verArticulos=1;
                //this.limpiarcliente();
            },
            mostrarObservacion(){
                this.verObservacion=1;
            },
            ocultarArticulos(){
                this.verArticulos=0;
            },
            ocultarObservacion(){
                this.verObservacion=0;
            },
            close () {
                this.verArticulos = false;
                this.verObservacion = false;
                //this.select();
            },
            agregarObservacion(){

                            let me=this;
                            axios.post('api/Correspondencia/CrearObservacion',{
                                'id_correspondencia':me.id_correspondencia,
                                'observacion':me.observacion,
                                'cambio_estado':"GENERADO-ASIGNADO",
                                'usuario': me.$store.state.usuario.nombre,
                            }).then(function(response){
                                me.listarObservacion(me.id_correspondencia);
                                me.verObservacion = false;
                                me.observacion = ""
                            }).catch(function(error){
                                console.log(error);
                            });
            },
            cerrarTramite(){

                            let me=this;
                            axios.post('api/Correspondencia/CrearObservacion',{
                                'id_correspondencia':me.id_correspondencia,
                                'observacion':me.observacion,
                                'cambio_estado':"CUMPLIDO-CERRADO",
                                'usuario': me.$store.state.usuario.nombre,
                            }).then(function(response){
                                me.cerrar();
                                me.ocultarNuevo();
                                me.listar();
                                me.limpiar();
                            }).catch(function(error){
                                console.log(error);
                            });
            },
            agregarCumplido(id){

                            let me=this;
                            axios.post('api/Correspondencia/CrearObservacion',{
                                'id_correspondencia':id,
                                'observacion':me.observacion,
                                'cambio_estado':"ASIGNADO-CUMPLIDO",
                                'usuario': me.$store.state.usuario.nombre,
                            }).then(function(response){
                                me.listarObservacion(me.id_correspondencia);
                                me.verObservacion = false;
                                me.observacion = ""
                            }).catch(function(error){
                                console.log(error);
                            });
            },
             listar(){
                if(store.state.usuario.rol == 1 || store.state.usuario.rol == 8 || store.state.usuario.rol == 6){
                   if(store.state.usuario.rol == 6){
                       this.RolSeguridad=true;
                   }
                    let me=this;
                    me.administrador = true;
                    axios.get('api/Correspondencia/Listar').then(function(response){
                        //console.log(response);
                        me.registros=response.data;
                    }).catch(function(error){
                        console.log(error);
                    });
                }else{
                    
                    let me=this;
                    axios.get('api/Correspondencia/listado/' + store.state.usuario.id_usuario,{}).then(function(response){
                        //console.log(response);
                        me.registros=response.data;
                    }).catch(function(error){
                        console.log(error);
                    });   
                }
            },
             listarfiltro(){
                let me=this;
                if(store.state.usuario.rol == 1){
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Permisos/ListarFiltro/'+me.search,configuracion).then(function(response){
                    //console.log(response);
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });
              }
            },
            listarCorreo(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Usuarios/Mostrar/'+id,configuracion).then(function(response){

                    me.correo=response.data["correo"];

                }).catch(function(error){
                    console.log(error);
                });
            },
            FiltroCliente(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.select, 500);
            },
            select(){
                let me=this;
                var clientesArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Remitente/Listar',configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.documento,value:x.id_remitente});
                        me.Nclientes.push({text: x.nombre,value:x.id_remitente});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            FiltroDocumento(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.selectDocumento, 500);
            },
            selectDocumento(){
                let me=this;
                var clientesArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                //axios.get('api/ClientesCall/Select',configuracion).then(function(response){
                axios.get('api/Remitente/Listar',configuracion).then(function(response){
                    clientesArray=response.data;
                    clientesArray.map(function(x){
                        me.clientes.push({text: x.documento,value:x.id_remitente});
                        me.Nclientes.push({text: x.nombre,value:x.id_remitente});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            selectMotivo(){
                let me=this;
                var motivoArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/TipoDocumento/Listar',configuracion).then(function(response){
                    motivoArray=response.data;
                    me.tipoDocumentos = response.data;
                    motivoArray.map(function(x){
                        me.motivos.push({text: x.nombre,value:x.id_tipodocumento});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             selectUsuarios(){
                let me=this;
                var usuariosArray=[];
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Usuarios/Listar',configuracion).then(function(response){
                    usuariosArray=response.data;
                    usuariosArray.map(function(x){
                        me.id_usuarios.push({text: x.nombre,value:x.id_usuario,
                        regional:x.regional,cargo:x.cargo});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             listarObservacion(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Correspondencia/ListarObservaciones/'+id,configuracion).then(function(response){
                    //console.log(response);
                    me.observaciones=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            sumarDias(fecha, dias){
                fecha.setDate(fecha.getDate() + dias - 1);
                return fecha.toISOString().substr(0, 10);
            },
            TipoDocumento(texto){
              for(var i=0;i<this.tipoDocumentos.length;i++){
                  if(this.tipoDocumentos[i]["id_tipodocumento"] == this.idmotivo){
                    this.prioridad = this.tipoDocumentos[i]["prioridad"];
                    var d = new Date();
                    var fechanueva = this.sumarDias(d, this.tipoDocumentos[i]["fecha_plazo"]);
                    this.fecha = fechanueva;
                  }
              }
            },
            verDetalles(item){
                var d = new Date();
                var fechanueva= this.sumarDias(d, item.plazo);
                this.limpiar();
                this.fecha = fechanueva;
                this.id_correspondencia=item.id_correspondencia;
                this.clientes.push({text: item.documento,value:item.id_remitente});
                this.Nclientes.push({text: item.remitente,value:item.id_remitente});
                this.idcliente=item.id_remitente;
                this.prioridad=item.prioridad;
                this.nombrecliente=item.id_remitente;
                this.idmotivo=item.id_tipodocumento;
                this.id_usuario=item.id_usuario;
                this.observacion=item.observacion;
                this.prioridad=item.prioridad;
                this.desdoc = item.desdoc;
                this.anexos = item.anexos;
                this.folios = item.folios;
                this.estado=item.estado;
                this.listarObservacion(item.id_correspondencia);
                this.verNuevo=1;
                
                if(item.estado == "CUMPLIDO"){
                    this.verDet=1;
                }else if(item.estado == "CERRADO"){
                    this.verDet=3; 
                }
                else{
                    this.verDet=2;
                }
            },
            limpiar(){
                this.id="";
                this.id_correspondencia="";
                this.idcliente="";
                this.idextencion=1;
                this.observacion="";
                this.idmotivo=4;
                this.prioridad="";
                this.id_usuario="";
                this.nombrecliente="";
                this.correo="";
                this.desdoc="";
                this.anexos="",
                this.folios="",
                this.observaciones=[];
                this.clientes=[];
                this.Nclientes=[];
            },
             limpiarcliente(){
                this.id_remitente="";
                this.telefono="";
                this.direccion="";
                this.ciudad="";
                this.nombre="";
                this.documento="";
                this.email="";
                this.error=null;
            },
            guardar () {
                this.counter=true;
                if (this.validar()){
                this.counter=false;
                    return;
                }
                this.observaciones.push({ observacion:this.observacion,cambio_estado:"GENERADO",usuario:this.$store.state.usuario.nombre});
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};                
                let me=this;
                me.listarCorreo(me.id_usuario);
                axios.post('api/Correspondencia/Crear',{

                    'id_remitente': me.idcliente,
                    'id_tipodocumento': me.idmotivo,
                    'id_usuario':me.id_usuario,
                    'fecha_cumplido':me.fecha,
                    'usuario':me.$store.state.usuario.id_usuario,
                    'desdoc':me.desdoc,
                    'anexos':me.anexos,
                    'folios':me.folios,
                    'observaciones':me.observaciones
                      
                },configuracion).then(function(response){
                    me.sendEmail();
                    Swal.fire({
                                //position: 'bot-end',
                                type: 'success',
                                title: 'Registro Guardado',
                                showConfirmButton: false,
                                timer: 1000
                                })
                    me.ocultarNuevo();
                    me.listar();
                    me.limpiar();                      
                }).catch(function(error){
                    console.log(error);
                });
            },
             actualizar () {
                
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};                
                let me=this;
                me.listarCorreo(me.id_usuario);
                axios.put('api/Correspondencia/Actualizar',{

                    'id_correspondencia':me.id_correspondencia,
                    'id_remitente':me.idcliente,
                    'id_usuario':me.id_usuario,
                    'id_tipodocumento':me.idmotivo,
                    'id_usuario':me.id_usuario,
                    'desdoc':me.desdoc,
                    'anexos':me.anexos,
                    'folios':me.folios,
                    'fecha_cumplido':me.fecha,
   
                    
                },configuracion)
                .then(function(response){
                   
                    me.agregarObservacion();
                    me.sendEmail();
                    Swal.fire({
                                //position: 'bot-end',
                                type: 'success',
                                title: 'Registro Actualizado',
                                showConfirmButton: false,
                                timer: 1000
                                })
                   
                    me.ocultarNuevo();
                    me.listar();
                    me.limpiar();
                                            
                }).catch(function(error){
                    console.log(error);
                });
            },
             guardarCliente () {

                let me=this;
                axios.get('api/Remitente/Consecutivo').then(function(response){
                    let id = response.data[0]["id_remitente"];      

                    //Código para guardar
                    axios.post('api/Remitente/Crear',{
                        'documento':me.documento,
                        'nombre': me.nombre,
                        'telefono':me.telefono,
                        'email': me.email,
                        'direccion':me.direccion,
                        'ciudad':me.ciudad
                    },).then(function(response){
                        me.close();
                        var Integro = parseInt(id);
                         me.clientes.push({text: me.documento,value:id});
                         me.Nclientes.push({text: me.nombre,value:id}); 
                         me.nombrecliente=Integro; 
                        me.limpiarcliente();                      
                    }).catch(err => {
                        if (err.response){
                            me.error=err.response.data;
                        }else{
                            me.error="Ocurrió un error";
                        }
                        //console.log(err)
                    })
                });   
            },
            AutomaticoCliente(item){
                this.verArticulos=0;
                var documento = this.documento;
                var Integro = parseInt(documento);
                  this.idcliente=Integro;0.
            },
            validar(){  
                this.valida=0;
                this.validaMensaje=[];

                if (!this.idcliente){
                    this.validaMensaje.push("Ingrese el remitente.");
                }
                // if (!this.idmotivo){
                //     this.validaMensaje.push("Seleccione un Motivo.");
                // }
                // if (this.observaciones == ""){
                //     this.validaMensaje.push("Ingresar Observación.");
                // }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.id_correspondencia;
                this.adId=item.id_correspondencia;                
                if (accion==1){
                    this.adAccion=1;
                }
                else if (accion==2){
                    this.adAccion=2;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            cerrar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Correspondencia/Cumplir/'+this.id_correspondencia+'/CERRADO',{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            cumplir(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Correspondencia/Cumplir/'+this.adId+'/CUMPLIDO',{},configuracion).then(function(response){
                    me.agregarCumplido(me.adId);
                      Swal.fire({
                                //position: 'bot-end',
                                type: 'success',
                                title: 'Cumplido Exitoso',
                                showConfirmButton: false,
                                timer: 1000
                                })
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }

</script>
