<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Roles</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>

                       <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="primary" dark class="mb-2">Nuevo</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field type="text" v-model="desc_perfil" label="Nuevo Rol">
                                    </v-text-field>
                                     <v-text-field  v-model="id_perfil" v-if="!dialog">
                                    </v-text-field>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                     <v-dialog v-model="verPaginas" max-width="1000px">
                         
                        <v-card>
                            <v-card-title>
                                <span class="headline">Formulario de Paginas</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container grid-list-md>
                                    <v-layout wrap>
                                        
                                        <v-flex xs12 sm12 md12 lg12 xl12>
                                             <v-autocomplete v-model="idpagina"
                                                :items="modulos" 
                                                label="Seleccionar Pagina a Ingresar "
                                                autocomplete>
                                                </v-autocomplete>
                                                <v-text-field v-model="idrol" v-if="!verPaginas">
                                                </v-text-field>

                                            <template>
                                               <v-data-table
                                                    :headers="cabeceraPaginas"
                                                    :items="paginas"
                                                    class="elevation-1"
                                                >
                                                    <template slot="items" slot-scope="props">
                                                        <td class="justify-center layout px-0">
                                                            <v-icon
                                                            small
                                                            class="mr-2"
                                                            @click="eliminarPagina(props.item.id_grupo)"
                                                            >
                                                            delete
                                                            </v-icon>
                                                        </td>
                                                        <td>{{ props.item.descripcion}}</td>
                                                        <td>{{props.item.tipopagina}}</td>
                                                        <td>{{props.item.fecha}}</td>
                                                    </template>
                                                    <template slot="no-data">
                                                        <h3>No hay paginas habilitadas para este Rol.</h3>
                                                    </template>
                                                </v-data-table> 
                                            </template>
                                        </v-flex>
                                    </v-layout>
                                </v-container>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn @click="adicionarPaginas()" color="blue darken" flat>
                                    Adicionar
                                </v-btn>
                                <v-btn @click="ocultarPaginas()" color="blue darken" flat>
                                    Cancelar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>


            <v-data-table
                :headers="headers"
                :items="roles"
                :search="search"
                class="elevation-1"
            >
                <template slot="items" slot-scope="props"> 
                     <td class="justify-center layout px-0">
                        <v-icon
                        small
                        class="mr-2"
                        @click="editItem(props.item)"
                        >
                        edit
                        </v-icon>
                        <template>
                            <v-icon
                            small
                            @click="mostrarPaginas(props.item.id_perfil)"
                            >
                            add
                            </v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.id_perfil }}</td>                   
                    <td>{{ props.item.desc_perfil }}</td>
                    <td>
                        <div v-if="props.item.estado">
                            <span class="blue--text">Activo</span>
                        </div>
                        <div v-else>
                            <span class="red--text">Inactivo</span>
                        </div>
                    </td>
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    export default {
        data(){
            return {
                roles:[],
                paginas:[], 
                modulos:[],
                editedIndex: -1,               
                dialog: false,
                verPaginas:false,
                idpagina:'' ,
                idrol:'' ,
                id_perfil:'' ,
                desc_perfil:'' ,
                headers: [
                    { text: 'Opciones', value: 'id_perfil',sortable: false },
                    { text: 'Id', value: 'id_perfil' },
                    { text: 'Nombre', value: 'desc_perfil' },
                    { text: 'Estado', value: 'condicion', sortable: false  }                
                ],
                cabeceraPaginas:[
                    { text: 'Opciones', value: 'id_grupo',sortable: false },
                    { text: 'Nombre', value: 'nombre',sortable: false  },
                    { text: 'Tipo de pagina', value: 'tipopagina', sortable: false  } ,  
                    { text: 'Fecha', value: 'fecha', sortable: false  }   
                ],
                search: ''             
            }
        },
        computed: {
             formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Rol' : 'Actualizar Rol'
            }
        },

        watch: {
        },

        created () {
            this.listar();
            this.listarPaginas();
        },
        methods:{
            mostrarPaginas(id){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get("api/Roles/ListaGrupos/"+ id,configuracion).then(function(response){

                   me.paginas = response.data;
                   me.idrol = id;
                
                }).catch(function(error){
                    console.log(error);
                });
                this.verPaginas=true;
               
            },
            ocultarPaginas(){
                this.verPaginas=false;
            },
            eliminarPagina(id){

                let me=this;
               
                axios.delete("api/Roles/Eliminar/"+ id).then(function(response){
                     me.mostrarPaginas(me.idrol); 
                     console.log("aqui estoy");
                }).catch(function(error){
                    console.log(error);
                });
            },
            adicionarPaginas(){
                    let me=this;
                    axios.post('api/Roles/Adicionar',{
                        'id_perfil':me.idrol,
                        'id_pagina':me.idpagina,

                    }).then(function(response){
                        me.mostrarPaginas(me.idrol);                      
                    }).catch(function(error){
                         console.log(error);
                     });
            },
            editItem (item) {
    
                this.id_perfil=item.id_perfil;
                this.desc_perfil = item.desc_perfil;
                this.editedIndex=1;
                this.dialog = true
            },
            limpiar(){
                this.desc_perfil='';
                this.id_perfil='';
                this.editedIndex=-1;
            },
             guardar () {

                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;
                    axios.put('api/Roles/Actualizar',{

                        'id_perfil':me.id_perfil,
                        'desc_perfil':me.desc_perfil
                                       
                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    axios.post('api/Roles/Crear',{

                        'desc_perfil':me.desc_perfil

                    }).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                   }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            close () {
                this.dialog = false;
            },
            listar(){
                let me=this;
                axios.get('api/Roles/Listar').then(function(response){
                    //console.log(response);
                    me.roles=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarPaginas(){
                let me=this;
                var paginasArray=[];
                axios.get('api/Roles/ListaPaginas').then(function(response){
                    paginasArray=response.data;
                    paginasArray.map(function(x){
                        me.modulos.push({text: x.nombre,value:x.id_pagina});
                    });
                    
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>
