<template>
    <v-layout align-start>
        <v-flex>
                <v-alert v-model="AlertaPermiso" outline type="error" dismissible>
                No tienes permisos para realizar esta Accion.
                </v-alert>

            <v-toolbar flat color="white">
                <v-toolbar-title>Permisos</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-btn  @click="listarfiltro()" color="primary" dark class="mb-2">Buscar</v-btn>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog"  
                                scrollable
                                persistent
                                max-width="50%">
                        <v-btn slot="activator" color="primary" dark class="mb-2" 
                        @click="Consecutivopermiso()"
                        >Nuevo</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>

                                <v-flex xs12 sm3 md3>
                                <v-text-field v-model="id_permiso" label="Codigo" readonly></v-text-field>
                                </v-flex>

                                <v-flex xs12 sm5 md5>
                                    <v-autocomplete v-model="id_empleado"
                                    :items="empleados" 
                                    label="Empleado"
                                    :search-input.sync="search1"
                                    v-on:keyup="FiltroEmpleados"
                                    autocomplete>
                                    </v-autocomplete>
                                </v-flex>

                                <v-flex xs12 sm4 md4>
                                    <v-select 
                                        :items="clasificapermiso"
                                        v-model="id_clasificapermiso"
                                        label="Clase de Permiso"
                                        single-line>
                                    </v-select>                          
                                </v-flex> 

                                <v-flex xs12 sm6 md6 v-show="!dialog">
                                    <v-text-field
                                        v-model="fechaHoy"
                                        readonly
                                    ></v-text-field>
                                </v-flex>

                                <v-flex xs12 sm12 md12>
                                    <v-select 
                                        :items="seleccionar"
                                        v-model="seleccionarHoD"
                                        label="Seleccionar Hora o Dia"
                                        single-line>
                                    </v-select> 
                                </v-flex>

                                <v-flex v-if="seleccionarHoD == 'H'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu2"
                                    :close-on-content-click="false"
                                    v-model="menu2"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_inicio"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_inicio"
                                        label="Fecha Inicial"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_inicio" @input="$refs.menu2.save(fecha_inicio)"></v-date-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarHoD == 'H'" xs12 sm4 md4 >
                                 <v-text-field v-model="hrs_permiso" label="Horas Totales" readonly></v-text-field>
                                </v-flex>

                                <v-flex v-show="seleccionarHoD == 'H'" xs12 sm12 md12>
                                    <v-menu
                                        ref="menu"
                                        :close-on-content-click="false"
                                        v-model="menu"
                                        :nudge-right="40"
                                        :return-value.sync="hora_inicio"
                                        lazy
                                        transition="scale-transition"
                                        offset-y
                                        full-width
                                        max-width="290px"
                                        min-width="290px">
                                        <v-text-field 
                                            slot="activator"
                                            v-model="hora_inicio"
                                            label="Hora de Salida"
                                            prepend-icon="access_time"
                                            readonly>
                                       </v-text-field>
                                    <v-time-picker v-model="hora_inicio" @change="$refs.menu.save(hora_inicio)" format="24hr"></v-time-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarHoD == 'H'" xs12 sm12 md12>
                                    <v-menu
                                    ref="menu1"
                                    :close-on-content-click="false"
                                    v-model="menu1"
                                    :nudge-right="40"
                                    :return-value.sync="hora_fin"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    max-width="290px"
                                    min-width="290px"
                                    >
                                    <v-text-field 
                                    slot="activator"
                                    v-model="hora_fin"
                                    label="Hora de Llegada"
                                    prepend-icon="access_time"
                                    v-on:change="calcular"
                                    readonly
                                    ></v-text-field>
                                    <v-time-picker v-model="hora_fin" @change="$refs.menu1.save(hora_fin)" format="24hr"></v-time-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-if="seleccionarHoD == 'D'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu2"
                                    :close-on-content-click="false"
                                    v-model="menu2"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_inicio"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_inicio"
                                        label="Fecha Inicial"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_inicio" @input="$refs.menu2.save(fecha_inicio)"></v-date-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarHoD == 'D'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu3"
                                    :close-on-content-click="false"
                                    v-model="menu3"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_final"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_final"
                                        label="Fecha Final"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_final" @input="$refs.menu3.save(fecha_final)"></v-date-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex xs12 sm6 md6>
                                  <v-select 
                                        :items="TipoDcto"
                                        v-model="seleccionarDcto"
                                        label="Tipo de Permiso"
                                        single-line>
                                    </v-select>   
                                </v-flex>

                              
                                <v-flex xs12 sm6 md6>
                                    <v-autocomplete v-model="id_reemplaza"
                                    :items="empleados" 
                                    label="Persona Quien Reemplaza"
                                    :search-input.sync="search2"
                                    v-on:keyup="FiltroEmpleados2"
                                    autocomplete>>
                                    </v-autocomplete>
                                </v-flex>

                                <v-flex xs12 sm12 md12 v-if="seleccionarDcto == '3'" >
                                    <v-select 
                                        :items="seleccionar"
                                        v-model="seleccionarCompensado"
                                        label="Seleccionar Hora o Dia Compensatorio"
                                        single-line>
                                    </v-select> 
                                </v-flex>

                                 <v-flex v-if="seleccionarCompensado == 'H' &  seleccionarDcto == '3'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu4"
                                    :close-on-content-click="false"
                                    v-model="menu4"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_inicio_compensado"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_inicio_compensado"
                                        label="Fecha Inicial"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_inicio_compensado" @input="$refs.menu4.save(fecha_inicio_compensado)"></v-date-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarCompensado == 'H' &  seleccionarDcto == '3'" xs12 sm4 md4 >
                                 <v-text-field v-model="hrs_permiso" label="Horas Totales" readonly></v-text-field>
                                </v-flex>

                                <v-flex v-show="seleccionarCompensado == 'H' &  seleccionarDcto == '3'" xs12 sm12 md12>
                                    <v-menu
                                        ref="menu5"
                                        :close-on-content-click="false"
                                        v-model="menu5"
                                        :nudge-right="40"
                                        :return-value.sync="hora_inicio_compensado"
                                        lazy
                                        transition="scale-transition"
                                        offset-y
                                        full-width
                                        max-width="290px"
                                        min-width="290px">
                                        <v-text-field 
                                            slot="activator"
                                            v-model="hora_inicio_compensado"
                                            label="Hora de Salida"
                                            prepend-icon="access_time"
                                            readonly>
                                       </v-text-field>
                                    <v-time-picker v-model="hora_inicio_compensado" @change="$refs.menu5.save(hora_inicio_compensado)" format="24hr"></v-time-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarCompensado == 'H' &  seleccionarDcto == '3'" xs12 sm12 md12>
                                    <v-menu
                                    ref="menu6"
                                    :close-on-content-click="false"
                                    v-model="menu6"
                                    :nudge-right="40"
                                    :return-value.sync="hora_fin_compensado"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    max-width="290px"
                                    min-width="290px"
                                    >
                                    <v-text-field 
                                    slot="activator"
                                    v-model="hora_fin_compensado"
                                    label="Hora de Llegada"
                                    prepend-icon="access_time"
                                    v-on:change="calcular"
                                    readonly
                                    ></v-text-field>
                                    <v-time-picker v-model="hora_fin_compensado" @change="$refs.menu6.save(hora_fin_compensado)" format="24hr"></v-time-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-if="seleccionarCompensado == 'D' &  seleccionarDcto == '3'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu7"
                                    :close-on-content-click="false"
                                    v-model="menu7"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_inicio_compensado"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_inicio_compensado"
                                        label="Fecha Inicial"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_inicio_compensado" @input="$refs.menu7.save(fecha_inicio_compensado)"></v-date-picker>
                                    </v-menu>
                                </v-flex>

                                <v-flex v-show="seleccionarCompensado == 'D' &  seleccionarDcto == '3'" xs12 sm6 md6>
                                  <v-menu
                                    ref="menu8"
                                    :close-on-content-click="false"
                                    v-model="menu8"
                                    :nudge-right="40"
                                    :return-value.sync="fecha_final_compensado"
                                    lazy
                                    transition="scale-transition"
                                    offset-y
                                    full-width
                                    min-width="290px"
                                    >
                                    <v-text-field
                                        slot="activator"
                                        v-model="fecha_final_compensado"
                                        label="Fecha Final"
                                        prepend-icon="event"
                                        readonly
                                    ></v-text-field>
                                 <v-date-picker v-model="fecha_final_compensado" @input="$refs.menu8.save(fecha_final_compensado)"></v-date-picker>
                                    </v-menu>
                                </v-flex>
                                                                 
                                <v-flex xs12 sm12 md12>
                                    <v-textarea v-model="motivo_permiso" label="Motivo" :rules="motivoRules" required></v-textarea>
                                </v-flex>

                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                                    </div>
                                </v-flex>

                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="closedialog">Cancelar</v-btn>
                                <v-btn v-if="this.estado == 'SOLICITADO'" color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==2">¿Aprobar Permiso?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==1">¿Anular Permiso?</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span v-if="adAccion==2">Aprobar </span>
                                <span v-if="adAccion==1">Anular </span>
                                el permiso {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="activar">
                                    Aprobar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="desactivar">
                                    Anular
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                     <v-dialog v-model="Actualizacion" max-width="600px">
                         <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="id_empleado" label="Cedula"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                <v-text-field v-model="fecha_registro" label="fecha_registro"></v-text-field>
                                </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <!--<v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>-->
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="categorias"
                :search="search"
                class="elevation-1"
              >
                <template slot="items" slot-scope="props">
                    <td class="justify-center layout px-0">
                        <template v-if="props.item.estado == 'SOLICITADO'">
                            <v-icon @click="crearPDF(props.item.id_permiso)" class="mr-2">print</v-icon>
                        </template>
                        <template v-else>
                            <v-icon class="mr-2">print</v-icon>
                        </template>
                        
                        <template>
                          <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
                        </template>
                        
                        <template v-if="props.item.estado == 'SOLICITADO'">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(1,props.item)"
                            >
                            block
                            </v-icon>
                        </template>
                        <template v-else>
                            <v-icon
                            small
                            >
                            lock
                            </v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.id_permiso }}</td>
                    <td>{{ props.item.id_empleado }}</td>
                    <td>{{ props.item.nombre }}</td>
                    <td>{{ props.item.fecha_registro.split("T")[0]}}</td>
                    <td>

                         <template v-if="esAdministrador && props.item.estado == 'SOLICITADO'">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(2,props.item)"
                            >
                            check
                            </v-icon>
                        </template>
                        <div v-else-if="props.item.estado == 'SOLICITADO'">
                            <span class="blue--text">Activo</span>
                        </div>
                        <div v-else-if="props.item.estado == 'CONCEDIDO'">
                            <span class="green--text">Aprobado</span>
                        </div>
                        <div v-else>
                            <span class="red--text">Anulado</span>
                        </div>
                    </td>
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>

    import axios from 'axios'
    import store from '../store'
    import jsPDF from 'jspdf'
    import 'jspdf-autotable';


    export default {
        data(){
            return {
                categorias:[],                
                dialog: false,
                headers: [
                    { text: 'Opciones', value: 'opciones', sortable: false },
                    { text: 'Codigo', value: 'id_permiso' },
                    { text: 'Cedula', value: 'id_empleado' },
                    { text: 'Nombre', value: 'nombre' },
                    { text: 'Fecha', value: 'fec_inicio' },
                    { text: 'Estado', value: 'estado', }                
                ],
                search: '',
                search1:null,
                search2:null,
                editedIndex: -1,
                id_permiso:'',
                id_clasificapermiso:null,
                clasificapermiso:[
                    { text: 'EPS',value:'1' },
                    { text: 'TERAPIAS',value:'2' },
                    { text: 'CALAMIDAD',value:'3' },
                    { text: 'PERSONAL',value:'4' },
                    { text: 'ESTUDIO' ,value:'5'},
                    { text: 'ARL' ,value:'6'},
                    { text: 'GESTION ADMINISTRATIVA',value:'7' }
                ],
                seleccionarDcto:null,
                TipoDcto:[
                    { text: 'DESCUENTO POR NOMINA',value:'0' },
                    { text: 'DESCUENTO POR VACACIONES',value:'1' },
                    { text: 'REMUNERADO',value:'2' },
                    { text: 'COMPENSADO',value:'3' }
                ],
                permisos:[],
                seleccionar:[
                    { text: 'HORA',value:'H' },
                    { text: 'DIA',value:'D' },
                ],
                id_empleado: null,
                empleados:[],
                hora_inicio: '00:00',
                hora_fin:'00:00' ,
                hora_inicio_compensado: '00:00',
                hora_fin_compensado:'00:00' ,
                seleccionarHoD:'',
                seleccionarCompensado:'',
                por_hora:'N',
                por_dia:'N',
                hrs_permiso:'0',
                id_reemplaza: '0',
                row: null,
                motivo_permiso:'',
                motivoRules: [
                    v => !!v || 'Motivo es Requerido',
                ],
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                Actualizacion:0,
                adAccion: 0,
                adNombre: '',
                adId: '',
                estado:'',
                fecha_registro: new Date().toISOString().substr(0, 10),
                fecha_inicio: new Date().toISOString().substr(0, 10),
                fecha_final: new Date().toISOString().substr(0, 10),
                fecha_inicio_compensado: new Date().toISOString().substr(0, 10),
                fecha_final_compensado: new Date().toISOString().substr(0, 10),
                fechaHoy:new Date().toISOString().substr(0, 10),
                menu: false,menu1:false,menu2: false,menu3: false,menu4:false,menu5: false,menu6:false,menu7: false,menu8: false,
                modal: false,
                time: null,
                reactive: false,
                AlertaPermiso: false,
                customFilter (item, queryText, itemText) {
                const hasValue = val => val != null ? val : ''
                const text = hasValue(item.name)
                const query = hasValue(queryText)
                return text.toString()
                .toLowerCase()
                .indexOf(query.toString().toLowerCase()) > -1  
                } 

            }
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'Nuevo Permiso' : 'Actualizar Permiso'
            },
             esAdministrador(){
                return this.$store.state.usuario && this.$store.state.usuario.rol =='1';
            }
        },

        watch: {
            dialog (val) {
            val || this.closedialog()
            }
        },

        created () {
            this.listar();
        },

        methods:{
                
                FiltroEmpleados(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.Empleados, 500);

                     },
                Empleados(){
                     
                        let me=this;
                        var empleadosArray=[];
                        axios.get('api/Categorias/ListarFiltroEmpleados/'+me.search1).then(function(response){
                            empleadosArray=response.data;
                            empleadosArray.map(function(x){
                                me.empleados.push({text: x.nombre_empleado,value:x.id_empleado});
                            });
                        }).catch(function(error){
                            console.log(error);
                        });
                    },
                FiltroEmpleados2(){
                    
                    clearTimeout(this.controlador);
                    this.controlador = setTimeout(this.Empleados2, 500);

                     },
                Empleados2(){
                     
                        let me=this;
                        var empleadosArray=[];
                        axios.get('api/Categorias/ListarFiltroEmpleados/'+me.search2).then(function(response){
                            empleadosArray=response.data;
                            empleadosArray.map(function(x){
                                me.empleados.push({text: x.nombre_empleado,value:x.id_empleado});
                            });
                        }).catch(function(error){
                            console.log(error);
                        });
                    },
                  crearPDF(id){

                        axios.get('api/Permisos/MostrarPDF/'+ id,{}).then(function(item){
                        var validareemplazo = item.data.id_reemplaza;

                        axios.get('api/Categorias/Mostrar/'+ item.data.id_reemplaza,{}).then(function(response){
                        var reemplazo = response.data.nombre_empleado;

                        axios.get('api/Permisos/Mostrar/'+ item.data.id_empleado,{}).then(function(response){
                        var CantidadPermisos = response.data.length

                        var fechaRegistro = item.data.fecha_registro.split("T");
                        var fechaInicio = item.data.fec_inicio.split("T");
                        var fechaHora = fechaInicio;
                        var fechaFinal = item.data.fec_final.split("T");
                        var solicitado = new Array ("","","","","");
                            if (solicitado){
