            listar(){
                if(store.state.usuario.rol == 1){
                let me=this;
                axios.get('api/permisos/listar').then(function(response){
                    //console.log(response);
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });}else{
                let me=this;
                axios.get('api/permisos/listado/' + store.state.usuario.nombre,{}).then(function(response){
                    //console.log(response);
                    me.categorias=response.data;
                }).catch(function(error){
                    console.log(error);
                });   
                }
            },
            listarfiltro(){
                let me=this;
                if(store.state.usuario.rol == 1){
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Permisos/ListarFiltro/'+me.search,configuracion).then(function(response){
                    //console.log(response);
                    me.categorias=response.data;
                }).catch(function(error){
