            Reemplazo(id_reemplaza){
                let me=this;
                 if(this.id_reemplaza !== 0){
                    axios.get('api/Categorias/Mostrar/'+ id_reemplaza,{}).then(function(response){
                    me.empleados.push({text: response.data.nombre_empleado,value:response.data.id_empleado});
                    console.log(me.empleados);
                    }).catch(function(error){
                    console.log(error);
                });
                }else{
                    this.id_reemplaza = "SIN REEMPLAZO";
                }
