            },
            Consecutivopermiso(){
                let me=this;
                me.limpiar();
                var permisosArray=[];
                axios.get('api/permisos/Consecutivopermiso').then(function(response){
                    me.permisos=response.data;
                    me.id_permiso = me.permisos[0]["id_permiso"];
                }).catch(function(error){
                    console.log(error);
                });
            },
            calcular(){
                var hora1 = this.hora_inicio.split(":");
                var hora2 = this.hora_fin.split(":");
                var horatotal = (parseInt(hora2[0]))-(parseInt(hora1[0]));
                var minutostotal = (parseInt(hora2[1]))-(parseInt(hora1[1]));

                if(minutostotal < 0){
                    minutostotal = minutostotal + 60;
                    horatotal = horatotal - 1;
                }
                this.hrs_permiso = (horatotal);
            },
            editItem (item) {

                
                this.empleados.push({text: item.nombre,value:item.id_empleado});
                this.Reemplazo(parseInt(item.id_reemplaza));
                this.id_permiso=item.id_permiso;
                var clasificacion = item.id_clasificapermiso;
                this.id_clasificapermiso=clasificacion.toString();
                this.por_hora=item.por_hora;
                if(item.por_hora == "S"){this.seleccionarHoD = "H";}else{this.seleccionarHoD = "D";   }
                if(item.dcto_nomina == "S"){this.seleccionarDcto = "0";}else if(item.dcto_vacaciones == "S"){  
                this.seleccionarDcto = "1";}else if(item.jefe_inmediato == "S"){  
                this.seleccionarDcto = "2";}else{
                this.seleccionarDcto = "3";}
                this.por_dia=item.por_dia;
                var fechaInicio = item.fec_inicio.split("T");
                this.fecha_inicio=fechaInicio[0];
                var fechaFinal = item.fec_final.split("T");
                this.fecha_final=fechaFinal[0];
                this.hora_inicio=item.hora_inicio;
                this.hora_fin=item.hora_fin;
                this.hrs_permiso=item.hrs_permiso;
                this.dcto_nomina=item.dcto_nomina;
                this.dcto_vacaciones=item.dcto_vacaciones;
                this.jefe_inmediato=item.jefe_inmediato;
                this.motivo_permiso = item.motivo_permiso;
                this.id_reemplaza = item.id_reemplaza;
                this.id_empleado = item.id_empleado;
                console.log( item.id_reemplaza);
                console.log( item.id_empleado);
                this.renumerado = item.renumerado;
                this.jefe_autoriza = item.jefe_autoriza;
                this.fecha_registro=item.fecha_registro;
                this.editedIndex=1;
                this.estado=item.estado;
                this.dialog = true

