            limpiar(){
                this.id_permiso="";
                this.id_clasificapermiso= "";
                this.id_empleado="";
                this.fecha_registro= new Date().toISOString().substr(0, 10);
                this.fecha_inicio= new Date().toISOString().substr(0, 10);
                this.fecha_final= new Date().toISOString().substr(0, 10);
                this.fechaHoy= new Date().toISOString().substr(0, 10);
                this.hora_inicio= '00:00';
                this.hora_fin='00:00';
                this.motivo_permiso="";
                this.id_reemplaza="";
                this.seleccionarDcto="";
                this.seleccionarHoD="";
                this.hrs_permiso='0';
                this.editedIndex=-1;
                this.estado="SOLICITADO";
    
