            guardar () {
                if (this.validar()){
                   return;
                }
                if (this.editedIndex > -1) {
                    //Código para editar
                    let me=this;
                    var dia = "N";var hora = "N";
                    var descuento = new Array ("N","N","N","N");
                    var remunerado = "S";

                    if(me.seleccionarHoD == 'H'){ hora = "S";}else{ dia = "S"; }
                    if(me.seleccionarDcto !== ""){descuento[me.seleccionarDcto]= "S";}
                    if(me.id_clasificapermiso == "4" || me.id_clasificapermiso == "5" ){remunerado = "N"}

                    axios.put('api/Permisos/Actualizar',{
                        'id_permiso': me.id_permiso,
                        'id_empleado': me.id_empleado,
                        'id_clasificapermiso':me.id_clasificapermiso,
                        'fecha_registro': me.fechaHoy,
                        'por_hora' : hora,
                        'por_dia': dia,
                        'fec_inicio':me.fecha_inicio,
                        'fec_final':me.fecha_final,
                        'hora_inicio':me.hora_inicio,
                        'hora_fin':me.hora_fin,
                        'hrs_permiso':me.hrs_permiso,
                        'dcto_nomina':descuento[0],
                        'dcto_vacaciones':descuento[1],
                        'jefe_inmediato':descuento[2],
                        'dir_nalgh':descuento[3],
                        'motivo_permiso': me.motivo_permiso,
                        'id_reemplaza':me.id_reemplaza,
                        'renumerado':remunerado,

                    }).then(function(response){
                        me.closedialog();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    let me=this;
                    var dia = "N";var hora = "N";
                    var descuento = new Array ("N","N","N","N");
                    var remunerado = "S";

                    if(me.seleccionarHoD == 'H'){ hora = "S";}else{ dia = "S"; }
                    if(me.seleccionarDcto !== ""){descuento[me.seleccionarDcto]= "S";}
                    if(me.id_clasificapermiso == "4" || me.id_clasificapermiso == "5" ){remunerado = "N"}

                    axios.get('api/permisos/Consecutivopermiso').then(function(response){
                    var permisos=response.data;
                    var id = me.permisos[0]["id_permiso"];

                    axios.post('api/Permisos/Crear',{
                        'id_permiso': id,
                        'id_empleado': me.id_empleado,
                        'id_clasificapermiso':me.id_clasificapermiso,
                        'fecha_registro': me.fechaHoy,
                        'por_hora' : hora,
                        'por_dia': dia,
                        'fec_inicio':me.fecha_inicio,
                        'fec_final':me.fecha_final,
                        'hora_inicio':me.hora_inicio,
                        'hora_fin':me.hora_fin,
                        'hrs_permiso':me.hrs_permiso,
                        'dcto_nomina':descuento[0],
                        'dcto_vacaciones':descuento[1],
                        'jefe_inmediato':descuento[2],
                        'dir_nalgh':descuento[3],
                        'estado': "SOLICITADO",
                        'motivo_permiso': me.motivo_permiso,
                        'id_reemplaza':me.id_reemplaza,
                        'renumerado':remunerado,
                        'jefe_autoriza': store.state.usuario.nombre
                    }).then(function(response){
                        me.crearPDF(id);
                        me.closedialog();
                        me.listar();
                        me.limpiar(); 
                                              
                    }).catch(function(error){
                        console.log(error);

                    })});
                }
            },
            validar(){
               this.valida=0;
               this.validaMensaje=[];

                if (!this.id_empleado){
                   this.validaMensaje.push("Ingresar el Empleado");
                }
                if (!this.id_clasificapermiso){
                   this.validaMensaje.push("Seleccione la clase del permiso");

                }
                if (!this.seleccionarHoD){
                   this.validaMensaje.push("Seleccionar Hora o Dia");

                }
                if (!this.seleccionarDcto){
                   this.validaMensaje.push("Seleccionar Tipo de Descuento");

                }
                if (!this.motivo_permiso){
                   this.validaMensaje.push("Ingresar Motivo");

                }
               if (this.validaMensaje.length){
                    this.valida=1;
               }
               return this.valida;
           },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.id_permiso;
                this.adId=item.id_permiso;                
                if (accion==2){
                    this.adAccion=2;
                }
                else if (accion==1){
                    this.adAccion=1;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                axios.put('api/Permisos/Activar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                axios.put('api/Permisos/Desactivar/'+this.adId,{}).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
