<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-btn>
                <download-excel
                        class = "btn"
                        :fetch   = "fetchData"
                        :fields = "json_fields"
                        :before-generate = "startDownload"
                        :before-finish = "finishDownload"
                        name    = "Empleados.xls"
                        type    = "xls">
                   <v-icon>print</v-icon>
                </download-excel>
        </v-btn>
        <v-toolbar-title>Empleados</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="verNuevo==0"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-btn v-if="verNuevo==0" @click="listar()" color="primary" dark class="mb-2">Buscar</v-btn>
        <v-spacer></v-spacer>
        <v-btn v-if="verNuevo==0" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>
        <v-btn v-if="verNuevo!==0" @click="ocultarNuevo" flat="flat" class="mb-2">Cancelar</v-btn>
        <v-dialog v-model="verArticulos" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">Seleccione un Empleado</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-text-field
                      append-icon="search"
                      class="text-xs-center"
                      v-model="texto"
                      label="Ingrese artículo a buscar"
                      @keyup.enter="listarArticulo()"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraArticulos"
                        :items="articulos"
                        class="elevation-1"
                      >
                        <template slot="items" slot-scope="props">
                          <td class="justify-center layout px-0">
                            <v-icon small class="mr-2" @click="agregarDetalle(props.item)">add</v-icon>
                          </td>
                          <td>{{ props.item.nombre }}</td>
                          <td>{{props.item.categoria}}</td>
                          <td>{{props.item.descripcion}}</td>
                          <td>{{props.item.stock}}</td>
                          <td>{{props.item.precio_venta}}</td>
                        </template>
                        <template slot="no-data">
                          <h3>No hay artículos para mostrar.</h3>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="ocultarArticulos()" color="blue darken" flat>Cancelar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">¿Activar Item?</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">¿Anular Ingreso?</v-card-title>
            <v-card-text>
              Estás a punto de
              <span v-if="adAccion==1">Activar</span>
              <span v-if="adAccion==2">Anular</span>
              el ítem {{ adNombre }}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">Cancelar</v-btn>
              <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                color="orange darken-4"
                flat="flat"
                @click="desactivar"
              >Anular</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>

      <v-data-table
        :headers="headers"
        :items="ingresos"
        :search="search"
        class="elevation-1"
        v-if="verNuevo==0"
      >
        <template slot="items" slot-scope="props">
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="verDetalles(props.item)">edit</v-icon>
            <!--<template >
              <v-icon small @click="activarDesactivarMostrar(2,props.item)">block</v-icon>
            </template>-->
            <template >
              <v-icon small @click="verPermisos(props.item)">list</v-icon>
            </template>
          </td>
          <td>{{ props.item.id_empleado }}</td>
          <td>{{ props.item.nombre_empleado}}</td>
          <td>{{ props.item.municipio }}</td>
          <td>{{ props.item.cargo }}</td>
          <td>{{ props.item.zona}}</td>
          <td>{{ props.item.fec_ingreso.split("T")[0] }}</td>
          <td>
            <div v-if="props.item.estado=='ACTIVO    '">
              <span class="blue--text">ACTIVO</span>
            </div>
            <div v-else>
              <span class="red--text">{{props.item.estado}}</span>
            </div>
          </td>
        </template>
        <template slot="no-data">
          <v-btn color="primary" @click="listar">Resetear</v-btn>
        </template>
      </v-data-table>


      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo == 1">
        <div>

    <v-stepper v-model="e1">
      <v-stepper-header>
        <template v-for="n in steps">
          <v-stepper-step
            :key="`${n}-step`"
            :step="n"
            :complete="e1 > n"
          v-bind:editable="editedIndex !== -1" 
          >
         {{ headersteps[n-1] }}
          </v-stepper-step>

          <v-divider v-if="n !== steps" :key="n"></v-divider>
        </template>
      </v-stepper-header>
      <v-stepper-items>
        <v-stepper-content
          v-for="n in steps"
          :step="n"
          :key="`${n}-content`"
        >
        <div v-if="e1==1">
          <v-layout row wrap>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="id_empleado" label="Documento"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="nombre_empleado" label="Nombre"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field  type="date" v-model="fec_ingreso" label="Fecha de Ingreso"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="date"  v-model="fec_ncto" label="Fecha de Nacimiento" v-on:change="ValidaFecha"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
              <v-autocomplete
                v-model="id_municipio"
                :items="municipios"
                label="Municipio"
                autocomplete
              ></v-autocomplete>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="celular" label="Celular"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="telefono_fijo" label="Telefono Fijo"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field  v-model="correo" label="Correo"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="direccion" label="Direccion"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="barrio" label="Barrio"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="comuna" label="Comuna"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select
              v-model="estrato"
              :items="estratos"
              label="Vivienda"
            ></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select
              v-model="id_estado_civil"
              :items="estadoscivil"
              label="Estado Civil"
            ></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="sexo" :items="sexos" label="Sexo"></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="libretamilitar" :items="select" label="Libreta Militar"></v-select>
           </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="transporte" :items="transportes" label="Medio de Transporte"></v-select>
           </v-flex>
          </v-layout>
          <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-for="v in validaDatospersonales" :key="v" v-text="v">
                </div>
          </v-flex>
        </div>

    <div v-if="e1==2">
        <v-layout row wrap>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete
              v-model="id_nivelacademico"
              :items="academicos"
              label="Nivel Academico"
              autocomplete
            ></v-autocomplete>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4 v-show="id_nivelacademico == 3 ||  id_nivelacademico== 4||
                      id_nivelacademico== 5">
            <v-text-field v-model="titulo" label="Título Otorgado"></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_eps" :items="epss" label="EPS" autocomplete></v-autocomplete>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_afp" :items="afps" label="AFP" autocomplete></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="grupo_sanguineo" :items="grupo_sanguineos" label="Grupo sanguineo"></v-select>
          </v-flex>
          
        </v-layout>
         <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-for="v in validaDatosEstudio" :key="v" v-text="v">
                </div>
          </v-flex>
    </div>

    <div v-if="e1==3">
        <v-layout row wrap>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="empresa" :items="empresas" label="Empresa"></v-select>
          </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_zona" :items="zonas" label="Regional" autocomplete></v-autocomplete>
          </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="sede" :items="sedes" label="Sede"></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="id_dependencia" :items="dependencias" label="Dependencia"></v-select>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-autocomplete v-model="id_area" :items="areas" label="Area" autocomplete></v-autocomplete>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
             <v-autocomplete v-model="id_cargo" :items="cargos" label="Cargo" autocomplete></v-autocomplete>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="jornada" :items="jornadas" label="Turno"></v-select>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4 v-show="id_cargo == 35">
            <v-select v-model="id_tipoconductor" :items="tiposconductor" label="Tipo Conductor"></v-select>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4 v-show="id_cargo == 35">
            <v-text-field v-model="placa_operativa" :items="jornadas" label="Placa Vehiculo"></v-text-field>
          </v-flex>
           <v-flex xs12 sm2 md2 lg2 xl2>
            <v-select v-model="recibedotacion" :items="select" label="Recibe Dotacion"></v-select>
          </v-flex>
           <v-flex xs12 sm2 md2 lg2 xl2>
            <v-select v-model="talla_bota" :items="calzados" label="Talla Calzado" v-show="recibedotacion == 'S'"></v-select>
           </v-flex>
            <v-flex xs12 sm2 md2 lg2 xl2>
            <v-select v-model="talla_camisa" :items="camisas" label="Talla Camisa"  v-show="recibedotacion == 'S'"></v-select>
           </v-flex>
            <v-flex xs12 sm2 md2 lg2 xl2>
            <v-select v-model="talla_pantalon" :items="pantalones" label="Talla Pantalon"  v-show="recibedotacion == 'S'"></v-select>
           </v-flex>
        </v-layout>
         <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-for="v in validaDatosDatosGenerales" :key="v" v-text="v">
                </div>
        </v-flex>
    </div>

    <div v-if="e1==4">
        <v-layout row wrap>

           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-show="id_estado_civil === 2 || id_estado_civil === 3" 
            type="number" v-model="cedulaconyuge" label="Documento Conyuge"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-show="id_estado_civil === 2 || id_estado_civil === 3" 
            v-model="nombreconyuge" label="Nombre Conyuge"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-show="id_estado_civil === 2 || id_estado_civil === 3" 
            type="date" v-model="fechaconyuge" label="Fecha de nacimiento"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select
              v-show="id_estado_civil === 2 || id_estado_civil === 3" 
              v-model="id_nivelacademicoconyuge"
              :items="academicos"
              label="Nivel Academico"
            ></v-select>
            </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select  
            v-show="id_estado_civil === 2 || id_estado_civil === 3" 
            v-model="laboraconyuge" :items="select" label="Labora" ></v-select>
          </v-flex>
           
        <v-flex xs12 sm12 md12 lg12 xl12>
                <div  v-for="v in validaDatosConyuge" :key="v" v-text="v">
                </div>
        </v-flex>
        </v-layout>
    </div>

     <div v-if="e1==5">
        <v-layout row wrap>

           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="tipodocumentodependiente" :items="tipodocumentodependientes" label="Tipo Documento" ></v-select>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="documentodependiente" label="Documento"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field v-model="nombredependiente" label="Nombre"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="parentescodependiente" :items="parentescodependientes" label="Parentesco" ></v-select>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field  type="date" v-model="fechadependiente" label="Fecha Nacimiento"></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-select v-model="sexodependiente" :items="sexos" label="Sexo"></v-select>
            </v-flex>
            <v-flex>
              <v-btn @click="agregarDetalle()" color="blue darken-1" flat>Adicionar</v-btn>
            <p></p>
                  <v-data-table
                        :headers="cabeceraArticulos"
                        :items="detalles"
                        class="elevation-1"
                      >
                        <template slot="items" slot-scope="props">
                          <td class="justify-center layout px-0">
                            <v-icon small class="mr-2" @click="eliminarDetalle(detalles,props.item)">delete</v-icon>
                          </td>
                          <td>{{props.item.tipo_doc}}</td>
                          <td>{{props.item.id_dependente}}</td>
                          <td>{{props.item.nombre}}</td>
                          <td>{{props.item.parentesco}}</td>
                          <td>{{props.item.fec_ncto}}</td>
                          <td>{{props.item.sexo}}</td>
                        </template>
                        <template slot="no-data">
                          <h3>No hay Dependientes para mostrar.</h3>
                        </template>
                      </v-data-table>
            </v-flex>
            <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                </div>
            </v-flex>
           <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-text="errorArticulo">
                </div>
           </v-flex>
        </v-layout>
    </div>

     <div v-if="e1==6">
        <v-layout row wrap>

           <v-flex xs12 sm3 md3 lg3 xl3>
            <v-select v-model="tipocontacto" :items="tiposcontactos" label="Tipo Contacto" ></v-select>
          </v-flex>
           <v-flex xs12 sm3 md3 lg3 xl3>
            <v-text-field  v-model="nombrecontacto" label="Nombre"></v-text-field>
          </v-flex>
           <v-flex xs12 sm3 md3 lg3 xl3>
            <v-text-field type="number" v-model="celular_contacto"  label="Celular" ></v-text-field>
          </v-flex>
          <v-flex xs12 sm3 md3 lg3 xl3>
            <v-text-field type="number" v-model="fijo_contacto"  label="Telefono Fijo" ></v-text-field>
          </v-flex>
             <v-btn @click="agregarContacto()" color="blue darken-1" flat>Adicionar</v-btn>
            <v-flex xs12 sm12 md12 lg12 xl12>
            
            <p></p>
                  <v-data-table
                        :headers="cabeceraContactos"
                        :items="contactos"
                        class="elevation-1"
                      >
                        <template slot="items" slot-scope="props">
                          <td class="justify-center layout px-0">
                            <v-icon small class="mr-2" @click="eliminarDetalle(contactos,props.item)">delete</v-icon>
                          </td>
                          <td>{{props.item.tipo_contacto}}</td>
                          <td>{{props.item.nombre_empleado}}</td>
                          <td>{{props.item.celular_contacto}}</td>
                          <td>{{props.item.fijo_contacto}}</td>
                        </template>
                        <template slot="no-data">
                          <h3>No hay Contactos para mostrar.</h3>
                        </template>
                      </v-data-table>
            </v-flex>
            <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                </div>
            </v-flex>
           <v-flex xs12 sm12 md12 lg12 xl12>
                <div class="red--text" v-text="errorArticulo">
                </div>
           </v-flex>
        </v-layout>
    </div>

    <p></p>
          <v-btn v-if="n !== 6" color="primary" @click="nextStep(n)">Siguiente</v-btn>
           <v-btn v-if="n == 6" color="primary"  @click.native="guardar">Guardar</v-btn>
          <v-btn  v-if="n !== 1" flat  @click="backStep(n)">Cancel</v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>


        </div>
      </v-container>

         <v-container grid-list-sm class="pa-4 white" v-if="verNuevo == 3">
        <v-layout row wrap>

            <!--<v-flex xs12 sm4 md4 lg4 xl4>
              <v-text-field v-model="nombre_empleado" label="Nombre"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md4 lg4 xl4>
              <v-text-field v-model="fec_ingreso" label="Fecha de Ingreso"></v-text-field>
            </v-flex>-->
                      <template>
                          <v-card
                            class="mt-4 mx-auto"
                            width="350"
                            height="200"
                          >
                            <v-card-text class="pt-0">
                              <div class="title font-weight-light mb-2">NOMINA</div>
                              <div class="subheading font-weight-light grey--text">Cantidad de Permisos</div>
                              <v-divider class="my-2"></v-divider>
                              <v-icon
                                class="mr-2"
                                small
                              >
                                mdi-clock
                              </v-icon>
                                <p style="font-size: 70px;"  class="text-lg-center font-weight-light grey--text">{{nomina}}</p>
                            </v-card-text>
                          </v-card>
                        </template>
                        <template>
                          <v-card
                            class="mt-4 mx-auto"
                            width="350"
                            height="200"
                          >
                            <v-card-text class="pt-0">
                              <div class="title font-weight-light mb-2">VACACIONES</div>
                              <div class="subheading font-weight-light grey--text">Cantidad de Permisos</div>
                              <v-divider class="my-2"></v-divider>
                              <v-icon
                                class="mr-2"
                                small
                              >
                                mdi-clock
                              </v-icon>

                               <p style="font-size: 70px;"  class="text-lg-center font-weight-light grey--text">{{vacaciones}}</p>

                            </v-card-text>
                          </v-card>
                        </template>
                        <template>
                          <v-card
                            class="mt-4 mx-auto"
                            width="350"
                            height="200"
                          >
                            <v-card-text class="pt-0">
                              <div class="title font-weight-light mb-2">REMUNERADO</div>
                              <div class="subheading font-weight-light grey--text">Cantidad de Permisos</div>
                              <v-divider class="my-2"></v-divider>
                              <v-icon
                                class="mr-2"
                                small
                              >
                                mdi-clock
                              </v-icon>
                               <p style="font-size: 70px;"  class="text-lg-center font-weight-light grey--text">{{remunerado}}</p>
                            </v-card-text>
                          </v-card>
                        </template>
                        <template>
                          <v-card
                            class="mt-4 mx-auto"
                            width="350"
                            height="200"
                          >
                            <v-card-text class="pt-0">
                              <div class="title font-weight-light mb-2">COMPENSADO</div>
                              <div class="subheading font-weight-light grey--text">Cantidad de Permisos</div>
                              <v-divider class="my-2"></v-divider>
                              <v-icon
