      this.editedIndex = 1;
    },
       verPermisos(item) {
         let me = this;
        axios.get('api/Permisos/Mostrar/'+ item.id_empleado,{}).then(function(response){
        var CantidadPermisos = response.data.length
        var nomina = 0;
        var vacaciones = 0;
        var remunerado = 0;
        var compensado = 0;

         for (var i = 0; i < CantidadPermisos; i++) {
              if(response.data[i].nomina == "S"){
                  nomina= nomina + 1;
              }
              else if(response.data[i].vacaciones == "S"){
                  vacaciones = vacaciones +1;
              }
              else if(response.data[i].remunerado == "S"){
                  remunerado = remunerado +1;
              }
               else if(response.data[i].compensado == "S"){
                  compensado = compensado +1;
              }
             }
          me.nomina = nomina;
          me.vacaciones = vacaciones;
          me.remunerado = remunerado;
          me.compensado = compensado;

        });
      this.verNuevo = 3;
