    validar(n) {
     
      this.valida = 0;
      this.validaDatospersonales = [];
      this.validaDatosEstudio=[];
      this.validaDatosDatosGenerales=[];

      if(n==1){

      if (!this.id_empleado) {
        this.validaDatospersonales.push("Ingresar Documento del Empleado.");
      }
      if (!this.nombre_empleado) {
        this.validaDatospersonales.push("Ingresar el Nombre del Empleado.");
      }
       if (!this.fec_ingreso) {
        this.validaDatospersonales.push("Ingresar Fecha Ingreso.");
      }
      if (!this.fec_ncto) {
        this.validaDatospersonales.push("Ingresar Fecha Nacimiento.");
      }
       if (!this.id_municipio) {
        this.validaDatospersonales.push("Ingresar Municipio.");
      }
      if (!this.celular) {
        this.validaDatospersonales.push("Ingresar Numero Celular.");
      }
       if (!this.correo) {
        this.validaDatospersonales.push("Ingresar Correo.");
      }
      if (!this.direccion) {
        this.validaDatospersonales.push("Ingresar Direccion.");
      }
       if (!this.barrio) {
        this.validaDatospersonales.push("Ingresar Barrio.");
      }
      if (!this.comuna) {
        this.validaDatospersonales.push("Ingresar Comuna.");
      } 
      if (!this.id_estado_civil) {
        this.validaDatospersonales.push("Seleccionar Estado Civil.");
      }
      if (!this.sexo) {
        this.validaDatospersonales.push("Seleccionar Sexo.");
      } 
      if (!this.libretamilitar) {
        this.validaDatospersonales.push("Seleccionar libreta Militar.");
      }
      if (!this.transporte) {
        this.validaDatospersonales.push("Seleccionar Medio de Transporte.");
      }
      if (this.validaDatospersonales.length) {
        this.valida = 1;
      }
      return this.valida;
    }
    else if(n==2){
      
      if (!this.id_nivelacademico) {
        this.validaDatosEstudio.push("Seleccionar Nivel Academico.");
      } 
      if (!this.id_eps) {
        this.validaDatosEstudio.push("Seleccionar Eps.");
      }
      if (this.id_afp==="") {
        this.validaDatosEstudio.push("Seleccionar Afp.");
      }
      if (this.validaDatosEstudio.length) {
        this.valida = 1;
      }

      return this.valida;
    }
    else if(n==3){

      if (!this.empresa) {
        this.validaDatosDatosGenerales.push("Seleccionar Empresa.");
      } 
      if (this.id_zona==="") {
        this.validaDatosDatosGenerales.push("Seleccionar Regional.");
      }
      if (!this.sede) {
        this.validaDatosDatosGenerales.push("Seleccionar Sede.");
      }
      if (!this.id_dependencia) {
        this.validaDatosDatosGenerales.push("Seleccionar Dependencia.");
      } 
      if (!this.id_area) {
        this.validaDatosDatosGenerales.push("Seleccionar Area.");
      }
      if (!this.id_cargo) {
        this.validaDatosDatosGenerales.push("Seleccionar Cargo.");
      }
      if (!this.jornada) {
        this.validaDatosDatosGenerales.push("Seleccionar Turno.");
      }
      if (!this.recibedotacion) {
        this.validaDatosDatosGenerales.push("Seleccionar Si recibe dotacion.");
      }
      if (this.validaDatosDatosGenerales.length) {
        this.valida = 1;
      }
      if (this.id_estado_civil === 1 || this.id_estado_civil === 4  || this.id_estado_civil === 5 ) {
        this.id_nivelacademicoconyuge = 6;this.fechaconyuge = "1900-01-01";this.cedulaconyuge = "0";this.nombreconyuge = "N/A"
        this.validaDatosConyuge.push("No existen datos para Conyuge.");
      }
      return this.valida;
    }

    },
     validardependiente() {
      this.valida = 0;
      this.validaMensaje = [];


      if (!this.tipodocumentodependiente) {
        this.validaMensaje.push("Seleccione un Tipo de documento.");
      }
      if (!this.documentodependiente) {
        this.validaMensaje.push("Ingrese Documento.");
      }
      if (!this.nombredependiente) {
        this.validaMensaje.push("Ingrese Nombre.");
      }
      if (!this.parentescodependiente) {
        this.validaMensaje.push("Ingrese Parentesco.");
      }
      if (!this.fechadependiente) {
        this.validaMensaje.push("Ingrese fecha.");
      }
       if (!this.sexodependiente) {
        this.validaMensaje.push("Seleccione el Sexo.");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.num_comprobante;
      this.adId = item.idingreso;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
