<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Reporte Covid-19</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                   
             </v-toolbar>


                <v-container grid-list-sm class="pa-4 white" fluid>
                    <v-layout row wrap>

                    <v-flex xs12 sm5 md5>
                                    <v-autocomplete v-model="id_empleado"
                                    type="number"
                                    :items="empleados" 
                                    :rules="Rules"
                                    label="Documento Empleado"
                                    :search-input.sync="search1"
                                    v-on:change="cambioNombre"
                                    v-on:keyup="FiltroEmpleados"
                                    autocomplete>
                                    </v-autocomplete>
                    </v-flex>

                     <v-flex xs12 sm5 md5>
                                    <v-autocomplete v-model="nombre"
                                    :items="nombres"
                                    label="Nombre Empleado"
                                     disabled
                                    autocomplete>
                                    </v-autocomplete>
                    </v-flex>

                    
                    <!-- <v-flex xs12 sm4 md6 lg6 xl6>
                        <v-text-field v-model="nombre" label="Nombre Empleado" disabled>
                        </v-text-field>
                    </v-flex> -->


                    <v-flex xs12 >
                        <v-card>
                            <v-list two-line subheader>
                                <v-card-title>¿Ha tenido usted alguno de estos sintomas en los últimos 3 días?</v-card-title>

                                  
                                <v-list-tile avatar  v-for="array in arreglo1" :key="array.model">
                                   
                                    <v-list-tile-content>
                                    <v-list-tile-title>{{array.nombre}}</v-list-tile-title>
                                    <v-list-tile-sub-title>{{array.pregunta}}</v-list-tile-sub-title>
                                    </v-list-tile-content>

                                    <v-list-tile-action>
                                       <v-switch @change="change(array.model)" v-model="datos[array.model]" 
                                       :label="`${datos[array.model]}`" value="SI" color="primary"></v-switch>
                                    </v-list-tile-action>

                                </v-list-tile>
                 

                            </v-list>
                        </v-card>
                    </v-flex>
                    
                    <v-flex xs12 sm12 md12 lg12 xl12>
                         <v-textarea v-model="otro_externo" label="Si la respuesta anterior es si, indicar caules síntomas" :rules="Rules">
                        </v-textarea>
                    </v-flex>

                    <v-flex xs12 sm4 md6 lg6 xl6>
                        <v-text-field type="number" v-model="temperatura" label="Temperatura Corporal" :rules="Rules">
                        </v-text-field>
                    </v-flex>

                    <v-flex xs12 sm4 md6 lg6 xl6>
                        <v-select v-model="transporte"
                        :items="transportes"
                        :rules="Rules"
                         label="Medio de Transporte">
                        </v-select>
                    </v-flex>

                     <v-flex xs12 >
                         <v-card>
                            <v-list two-line subheader>
                                <v-card-title>¿Pertenece algún grupo de riesgo?</v-card-title>

                                  
                                <v-list-tile avatar  v-for="arreglo in arreglo2" :key="arreglo.model">
                                   
                                    <v-list-tile-content>
                                    <v-list-tile-title>{{arreglo.nombre}}</v-list-tile-title>
                                    <v-list-tile-sub-title>{{arreglo.pregunta}}</v-list-tile-sub-title>
                                    </v-list-tile-content>

                                    <v-list-tile-action>
                                        <v-switch @change="change2(arreglo.model)" v-model="datos2[arreglo.model]" 
                                       :label="`${datos2[arreglo.model]}`" value="SI" color="primary"  ></v-switch>
                                    </v-list-tile-action>

                                </v-list-tile>
 
                            </v-list>
                         </v-card>
                    </v-flex>


                    <v-flex xs12 >
                        <v-card>
                            <v-card-title><b>Firma del Empleado</b></v-card-title>
                                <div class="container">
                                    <div class="row">
                                        <div class="col-12 mt-2">
                                        <VueSignaturePad
                                            id="signature"
                                            width="100%"
                                            height="500px"
                                            ref="signaturePad"
                                        />
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-3 mt-2">
                                        <v-btn @click="undo" flat>Limpiar</v-btn>
                                        </div>
                                    </div>
                                </div>
                         </v-card>
                    </v-flex>

                
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <!-- <div class="red--text"  v-for="v in validaMensaje" :key="v" v-text="v">
                        </div> -->
                    </v-flex>
      
                   
                        <v-btn @click="close()" color="blue darken-1" flat>Cancelar</v-btn>
                        <v-spacer></v-spacer>
                        <v-btn   v-on:click="guardar()" color="success" right>Guardar</v-btn>
                  

                    </v-layout>
                </v-container>
                <v-container>
                </v-container>

               
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    // import VueSweetalert2 from 'vue-sweetalert2'
    import Swal from 'sweetalert2'
    export default {
        data(){
            return {
                id_empleado:"",
                nombre:"",
                search1:null,
                empleados:[],
                nombres:[],
                arreglo1:[
                    {nombre:"Fiebre",model:"fiebre",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Fatiga",model:"fatiga",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Tos",model:"tos",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Deficultad para respirar",model:"dif_respirar",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Vómito",model:"vomito",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Diarrea",model:"diarrea",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Congestión Nasal",model:"cong_nasal",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Dolor de garganta",model:"dolor_garganta",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Escalofrio",model:"escalofrio",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Dolor muscular",model:"dolor_muscular",pregunta:"Ha tenido sintomas?"},
                    {nombre:"Otro",model:"otro",pregunta:"Cualquier otro sintoma relacionado con una enfermedad respiratoria"},
                    {nombre:"En los ultimos 14 dias",model:"contacto_externo",pregunta:"ha estado en contacto con personas que tuvieran alguno de los anteriores sintomas?"},
                ],
                datos:{
                    fiebre:"NO",
                    fatiga:"NO",
                    tos:"NO",
                    dif_respirar:"NO",
                    vomito:"NO",
                    diarrea:"NO",
                    cong_nasal:"NO",
                    dolor_garganta:"NO",
                    escalofrio:"NO",
                    dolor_muscular:"NO",
                    otro:"NO",
                    contacto_externo:"NO",
                },
                otro_externo:"N/A",
                temperatura:"",
                transporte:"",
                transportes:[
                    {  text: 'Bicicleta',value: 'Bicicleta' },
                    {  text: 'Motocicleta',value: 'Motocicleta' } , 
                    {  text: 'Carro Propio',value: 'Carro Propio' } , 
                    {  text: 'Transporte Publico',value: 'Transporte Publico' },
                    {  text: 'Otros',value: 'Otros' }  
                ],
                arreglo2:[
                    {nombre:"Mayor de 60 años",model:"edad_maxima",pregunta:"si su edad supera los 60 años"},
                    {nombre:"Hipertension",model:"hipertension",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Diabetes",model:"diabetes",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Cancer",model:"cancer",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Enfermedades de corazón",model:"enfer_corazon",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"patología pulmonar",model:"pat_pulmonar",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Enfermedad Renal crónica",model:"renal",pregunta:"si se ha diagnósticado alguna veza"},
                    {nombre:"Inmunosupresion",model:"inmunosupresion",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Obesidad",model:"obesidad",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Corticoides Recientemente",model:"corticoides",pregunta:"si se ha diagnósticado alguna vez"},
                    {nombre:"Fumador",model:"fumador",pregunta:"si fuma con frecuencia"},
                    {nombre:"Desinfeccion y lavado antes de ingresar a las intalaciones",model:"desinfeccion",pregunta:"ha realizado el debido procedimiento de lavado"},

                ],
                datos2:{
                    edad_maxima:"NO",
                    hipertension:"NO",
                    diabetes:"NO",
                    cancer:"NO",
                    enfer_corazon:"NO",
                    pat_pulmonar:"NO",
                    renal:"NO",
                    inmunosupresion:"NO",
                    obesidad:"NO",
                    corticoides:"NO",
                    fumador:"NO",
                    desinfeccion:"NO",
                },
                validaMensaje:"",
                Rules: [
                     v => !!v || 'Este campo es requerido',
                ],
                           
            }
        },
        created () {

        },
        methods:{
            undo() {
                this.$refs.signaturePad.undoSignature();
            },
            FiltroEmpleados(){
                    clearTimeout(this.controladorTiempo);
                    this.controladorTiempo = setTimeout(this.Empleados, 500);

            },
            Empleados(){
                     
                        let me=this;
                        var empleadosArray=[];
                        axios.get('api/Categorias/ListarFiltroEmpleados/'+me.search1).then(function(response){
                            empleadosArray=response.data;
                            empleadosArray.map(function(x){
                                me.empleados.push({text: x.id_empleado,value:x.id_empleado});
                                me.nombres.push({text: x.nombre_empleado,value:x.id_empleado});
                                //me.nombre = x.nombre_empleado;
                            });
                        }).catch(function(error){
                            console.log(error);
                        });
            },
            cambioNombre(){
                this.nombre = this.id_empleado;
                console.log("asd");
            },
            change(sel){
                let v=this.datos;
                if(v[sel]==null){
                    v[sel] = "NO";
                }
              
            },
            change2(sel){
                let v=this.datos2;
                if(v[sel]==null){
                    v[sel] = "NO";
                }
            },
            close () {
                this.limpiar();
            },
            limpiar(){
                this.id_empleado="";
                this.otro_externo="N/A";
                this.temperatura="";
                this.transporte="";
                this.$refs.signaturePad.clearSignature();
    
                let vardatos=this.datos;
                for(let i=0;i<12;i++){
                    vardatos[this.arreglo1[i].model] = "NO";
                    
                }
                let vardatos2=this.datos2;
                for(let i=0;i<12;i++){
                    vardatos2[this.arreglo2[i].model] = "NO";
                }
            },
            guardar () {
                if (this.validar()){
                    let me = this;
                     Swal.fire({
                                    type: 'error',
                                    title: me.validaMensaje[0],
                                    showConfirmButton: false,
                                    timer: 2500
                            })
                    return;
                }             
                    //Código para guardar
                    let me=this;
                    const { isEmpty, data } = this.$refs.signaturePad.saveSignature();

                    axios.post('api/Encuesta/Crear',{
                        'id_empleado':me.id_empleado,
                        'fiebre':me.datos['fiebre'],
                        'fatiga':me.datos['fatiga'],
                        'tos':me.datos['tos'],
                        'dif_respirar':me.datos['dif_respirar'],
                        'vomito':me.datos['vomito'],
                        'diarrea':me.datos['diarrea'],
                        'cong_nasal':me.datos['cong_nasal'],
                        'dolor_garganta':me.datos['dolor_garganta'],
                        'escalofrio':me.datos['escalofrio'],
                        'dolor_muscular':me.datos['dolor_muscular'],
                        'otro':me.datos['otro'],
                        'contacto_externo':me.datos['contacto_externo'],
                        'otro_externo':me.otro_externo,
                        'temperatura':me.temperatura,
                        'transporte':me.transporte,
                        'edad_maxima':me.datos2['edad_maxima'],
                        'hipertension':me.datos2['hipertension'],
                        'diabetes':me.datos2['diabetes'],
                        'cancer':me.datos2['cancer'],
                        'enfer_corazon':me.datos2['enfer_corazon'],
                        'pat_pulmonar':me.datos2['pat_pulmonar'],
                        'renal':me.datos2['renal'],
                        'inmunosupresion':me.datos2['inmunosupresion'],
                        'obesidad':me.datos2['obesidad'],
                        'corticoides':me.datos2['corticoides'],
                        'fumador':me.datos2['fumador'],
                        'desinfeccion':me.datos2['desinfeccion'],
                        'firma':data

                    }).then(function(response){

                         Swal.fire({
                                type: 'success',
                                title: 'Registro Guardado Exitosamente',
                                showConfirmButton: false,
                                timer: 2000
                                })

                        me.close();
                        //me.limpiar(); 

                    }).catch(err => {
                        
                        console.log(err);
                        if (err.response.status==400){

                            Swal.fire({
                                    type: 'error',
                                    title: err.response.data,
                                    showConfirmButton: false,
                                    timer: 2500
                            })

                        } else if (err.response.status==404){
                            //this.error="Usuario o Contraseña son incorrectos";
                        }else{
                            //this.error="Ocurrió un error";
                        }
                        return this.valida;
                        //console.log(err)
                     });
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.temperatura.length>2){
                    this.validaMensaje.push("No ingresar mas de dos digitos en el campo de temperatura.");
                }
                if (!this.id_empleado){
                    this.validaMensaje.push("Ingrese el Empleado.");
                }
                 if (!this.temperatura){
                    this.validaMensaje.push("Ingrese temperatura.");
                }
                 if (!this.otro_externo){
                    this.validaMensaje.push("Validar campo Otros.");
                }
                if (!this.transporte){
                    this.validaMensaje.push("Seleccionar medio de transporte.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
          
        }        
    }
</script>
