                                 <v-checkbox  v-model="chkpmatrimonio" class="mx-2" label="Partida de matrimonio/ Dec. Extrajucio"></v-checkbox>
                                  <v-checkbox  v-model="chkregistroh" class="mx-2" label="2 Fotocopias del registro civil(Hijos)"></v-checkbox>
                                   <v-checkbox  v-model="chkidentidadh" class="mx-2" label="2 Fotocopias tarjeta de identidad(7años)"></v-checkbox>
                                    <v-checkbox  v-model="chkestudiosh" class="mx-2" label="Fotocopia del certificado de estudios(Hijos)"></v-checkbox>
                                </v-flex>
                                  <v-flex xs12 sm4 md4>
                                     <v-checkbox  v-model="chkregistro" class="mx-2" label="fotocopia Registro Civil trabajador"></v-checkbox>
                                      <v-checkbox  v-model="chkfpadres" class="mx-2" label="Fotocopia ampliada de padres"></v-checkbox>
                                       <v-checkbox  v-model="chkepsc" class="mx-2" label="Afiliación EPS Conyuge"></v-checkbox>
                                        <v-checkbox  v-model="chkepsh" class="mx-2" label="Afiliación EPS Hijos"></v-checkbox>
                                </v-flex>
                                 <v-flex xs12 sm4 md4>
                                      <v-checkbox  v-model="chkepsp" class="mx-2" label="Afiliación EPS Padres"></v-checkbox>
                                      <v-checkbox  v-model="chkcajac" class="mx-2" label="Afiliación Caja Conyuge"></v-checkbox>
                                       <v-checkbox  v-model="chkcajah" class="mx-2" label="Afiliación Caja Hijos"></v-checkbox>
                                        <v-checkbox  v-model="chkcajap" class="mx-2" label="Afiliación Caja Padres"></v-checkbox>
                                </v-flex>

                                <v-flex md12 >
