                                </v-flex>
                                <v-flex xs12 sm4 md4>
                                <v-checkbox  v-model="chkfosyga" class="mx-2" label="Consulta Fosyga"></v-checkbox>
                                 <v-checkbox  v-model="chkruaf" class="mx-2" label="Consulta RUAF"></v-checkbox>
                                  <v-checkbox  v-model="chkcbancaria" class="mx-2" label="Certificación bancaria"></v-checkbox>
                                   <v-checkbox  v-model="chkcontrato" class="mx-2" label="Contrato de trabajo"></v-checkbox>
                                    <v-checkbox  v-model="chkclausulas" class="mx-2" label="Clausulas Adicionales"></v-checkbox>
                                     <v-checkbox  v-model="chkficha" class="mx-2" label="Ficha de Ingreso"></v-checkbox>
                                </v-flex>
