                                      <v-checkbox  v-model="chkepse" class="mx-2" label="Afiliación EPS"></v-checkbox>
                                      <v-checkbox  v-model="chkafpe" class="mx-2" label="Afiliación AFP"></v-checkbox>
                                       <v-checkbox  v-model="chkcaja" class="mx-2" label="Afiliación Caja"></v-checkbox>
                                </v-flex>
                                

