                            </v-icon>
                        </template>

                        <template v-if="props.item.estado=='APROBADO'">
                            <v-icon 
                                small class="mr-2" @click="modalchecklist(props.item)">tab
                            </v-icon>
                        </template>
                         <template v-else>
                            <v-icon 
                                small class="mr-2">tab
                            </v-icon>
                        </template>
            
