            actualizar () {
                if (this.validarActualizar()){
                    return;
                }
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};                
                let me=this;

                axios.put('api/Requisiciones/Actualizar',{

                    'idrequisicion':me.idrequisicion,
                    'basico':me.basico,
                    'hed':me.hed,
                    'bonificacion':me.bonificacion,
                    'hen':me.hen,
                    'rn':me.rn,
                    'auxmovilizacion':me.auxmovilizacion,
                    'subtransporte':me.subtransporte,
                    'dominical':me.dominical,
                    'comision':me.comision,
                    'rodamiento':me.rodamiento,
                    'registro':me.registro,
                    'estado':me.estado,
                   
