<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <!--<v-btn @click="crearPDF()"><v-icon>print</v-icon></v-btn>-->
                <v-toolbar-title>Entrega de Turno</v-toolbar-title>
                    <v-divider
                    class="mx-2"
                    inset
                    vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                    <v-spacer></v-spacer>
                    <v-btn @click="CloseTurn" color="danger" dark class="mb-2" v-show="turno">Cerrar Turno</v-btn>
                    <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="primary" dark class="mb-2" v-show="!turno">Nuevo Turno</v-btn>
                        <v-card>
                            <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                
                            <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>

                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <v-text-field v-model="idTareas" label="idTareas"></v-text-field>
                                </v-flex>

                                <v-flex xs12 sm12 md12 v-show="editedIndex == 1">
                                    <v-textarea v-model="observacion" label="Observacion"></v-textarea>
                                </v-flex>

                                 <v-flex xs12 sm12 md12 v-show="editedIndex == -1">
                                    <v-autocomplete v-model="idTurno"
                                        :items="turnos" 
                                        label="Turno"
                                        :search-input.sync="search1"
                                        autocomplete>
                                    </v-autocomplete>
                                </v-flex>
                              
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                                    </div>
                                </v-flex>

                                </v-layout>
                            </v-container>
                            </v-card-text>
                
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" flat @click.native="close">Cancelar</v-btn>
                                <v-btn color="blue darken-1" flat @click.native="guardar">Guardar</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="adModal" max-width="290">
                        <v-card>
                            <v-card-title class="headline" v-if="adAccion==1">¿Cumplir Tarea?</v-card-title>
                            <v-card-title class="headline" v-if="adAccion==2">¿Eliminar Tarea?</v-card-title>
                            <v-card-text>
                                Estás a punto de 
                                <span v-if="adAccion==1">Cumplir </span>
                                <span v-if="adAccion==2">¿Eliminar </span>
                                la Tarea {{ adNombre }}
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                    Cancelar
                                </v-btn>
                                <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                    Cumplir
                                </v-btn>
                                <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="desactivar">
                                    Eliminar
                                </v-btn>
                            </v-card-actions>

                        </v-card>
                    </v-dialog>
                </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="tareas"
                :search="search"
                class="elevation-1"
            >
                <template slot="items" slot-scope="props">
                    <td class="justify-center layout px-0">
                        <v-icon
                        small
                        class="mr-2"
                        @click="editItem(props.item)"
                        >
                        edit
                        </v-icon>
                        <template v-if="props.item.condicion">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(2,props.item)"
                            >
                            block
                            </v-icon>
                        </template>
                        <template v-else>
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(1,props.item)"
                            >
                            check
                            </v-icon>
                        </template>
                    </td>
                    <td>{{ props.item.tarea}}</td>
                    <td>{{ props.item.turno}}</td>
                    <td>{{ props.item.observacion }}</td>
                    <td>{{ props.item.estado }}</td>
                    <td>{{ props.item.fechaCreacion }}</td>
                    <td>{{ props.item.fechaCierre }}</td>
                    <td>{{ props.item.usuario }}</td>
                   
                </template>
                <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios'
    import store from '../store'
    import jsPDF from 'jspdf'
    import autoTable from 'jspdf-autotable';
    export default {
        data(){
            return {
                tareas:[], 
                turnos:[],               
                dialog: false,
                headers: [
                    { text: 'Opciones', value: 'estado', sortable: false },
                    { text: 'Tarea', value: 'tarea' },
                    { text: 'Turno', value: 'turno' },
                    { text: 'Observacion', value: 'observacion' },
                    { text: 'Estado', value: 'estado' },
                    { text: 'Fecha Creacion', value: 'fechaCreacion' },
                    { text: 'Fecha Cierre', value: 'fechaCierre', sortable: false  },   
                    { text: 'Usuario', value: 'usuario', sortable: false  }              
                ],
                search: '',
                search1:'',
                turno:false,
                editedIndex: -1,
                NextTurn:0,
                id: '',
                idTareas:0,
                idTarea:'',
                idTurno:'',
                nombre: '',
                observacion:'',
                fecha:'',
                valida: 0,
                validaMensaje:[],
                adModal: 0,
                adAccion: 0,
                adNombre: '',
                adId: ''             
            }
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? 'Siguiente Turno' : 'Actualizar Turno'
            }
        },

        watch: {
            dialog (val) {
            val || this.close()
            }
        },

        created () {
            this.listar();
            this.consecutivo();
            this.listarTurno();
        },
        methods:{
            crearPDF(){
                var columns = [
                    {title: "Documento", dataKey: "Documento"},
                    {title: "Nombre", dataKey: "nombre"},
                    {title: "Código", dataKey: "codigo"}, 
                    {title: "Categoría", dataKey: "categoria"}, 
                    {title: "Stock", dataKey: "stock"},
                ];
                var rows = [];

                this.clientes.map(function(x){
                    rows.push({nombre:x.nombre,codigo:x.codigo,categoria:x.categoria,stock:x.stock});
                });

                // Only pt supported (not mm or in)
                var doc = new jsPDF('p', 'pt');
                doc.autoTable(columns, rows, {
                    margin: {top: 60},
                    addPageContent: function(data) {
                        doc.text("Listado de Artículos", 40, 30);
                    }
                });
                doc.save('Clientes.pdf');
            },
            listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Tareas/Listar',configuracion).then(function(response){
                    //console.log(response);
                    me.tareas=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarTurno(){
                 let me=this;
                 var TurnosArray=[];
                 let header={"Authorization" : "Bearer " + this.$store.state.token};
                 let configuracion= {headers : header};
                  axios.get('api/Turno/Listar',configuracion).then(function(response){
                            TurnosArray=response.data;
                            TurnosArray.map(function(x){
                                me.turnos.push({text: x.nombre,value:x.idTurno});
                            });
                        }).catch(function(error){
                            console.log(error);
                        });
            },
            editItem (item) {

                this.idTareas=item.idTareas;
                this.nombre=item.nombre;
                this.fecha=item.fecha;
                this.editedIndex=1;
                this.dialog = true
            },
            close () {
                this.dialog = false;
                this.limpiar();
            },
            limpiar(){
                this.id="";
                this.idTareas="";
                this.idTarea="";
                this.observacion="";
                this.fecha="";
                this.editedIndex=-1;
            },
            guardar () {
                //if (this.validar()){
                //    return;
                //}
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                if (this.editedIndex > -1) {
                    //Código para editar
                  
                    let me=this;
                    axios.put('api/Tareas/Actualizar',{

                        'idTareas':me.idTareas,
                        'observacion':me.observacion

                    },configuracion).then(function(response){
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                } else {
                    //Código para guardar
                    //this.consecutivo();
                    let me=this;

                    axios.post('api/Tareas/CrearTurnos',{

                        'idTurno':me.idTurno,
                        
                    },configuracion).then(function(response){
                        me.NewTurn();
                        me.close();
                        me.listar();
                        me.limpiar();                        
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            NewTurn(){
               
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                    
                    let me=this;
                    axios.put('api/Tareas/AbrirTurno',{

                        'idTurno':me.idTurno,
                        'usuario':store.state.usuario.nombre

                    },configuracion).then(function(response){   
                        me.consecutivo();                     
                    }).catch(function(error){
                        console.log(error);
                    });
            },
            CloseTurn(){
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                    
                    let me=this;
                    axios.put('api/Tareas/CerrarTurno',{

                        'usuario':store.state.usuario.nombre

                    },configuracion).then(function(response){   
                        me.consecutivo();
                        me.listar();
                    }).catch(function(error){
                        console.log(error);
                    });
            },
            consecutivo(){
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                let me=this;
                    axios.get('api/Tareas/ListarConsecutivo',configuracion).then(function(response){
                       
                        if (response.data[0]["idTurno"]==1){me.NextTurn=2;}
                        else if (response.data[0]["idTurno"]==2){me.NextTurn=3}
                        else {me.NextTurn=1}
                        me.idTurno=me.NextTurn;
                        me.turno = response.data[0]["estado"]; 
                    }).catch(function(error){
                        console.log(error);
                    }); 
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if (this.nombre.length<3 || this.nombre.length>50){
                    this.validaMensaje.push("El nombre debe tener más de 3 caracteres y menos de 50 caracteres.");
                }
                if (this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            activarDesactivarMostrar(accion,item){
                this.adModal=1;
                this.adNombre=item.idTareas;
                this.adId=item.idTareas;                
                if (accion==1){
                    this.adAccion=1;
                }
                else if (accion==2){
                    this.adAccion=2;
                }
                else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.put('api/Tareas/Activar/'+this.adId+'/'+store.state.usuario.nombre,{},configuracion).then(function(response){

                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar(); 

                }).catch(function(error){
                    console.log(error);
                });
            },
            desactivar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.delete('api/Remitente/Eliminar/'+this.adId,{},configuracion).then(function(response){
                    me.adModal=0;
                    me.adAccion=0;
                    me.adNombre="";
                    me.adId="";
                    me.listar();                       
                }).catch(function(error){
                    console.log(error);
                });
            }
        }        
    }
</script>
