<template>
  <v-layout align-start>
    <v-flex xs12 sm8>
      <v-container grid-list-sm class="pa-4 white">
        <v-toolbar flat color="white">
          <v-toolbar-title>Perfil de Usuario</v-toolbar-title>
          <v-divider class="mx-2" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <p>Información de Perfil</p>
        </v-toolbar>
        <v-layout row wrap>
         <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="number" v-model="documento"  label="Documento" readonly></v-text-field>
          </v-flex>
          <v-flex xs12 sm8 md8 lg8 xl8>
            <v-text-field type="text" v-model="nombre"  label="Nombre" readonly></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="text" v-model="rol"  label="Rol" readonly></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="text" v-model="regional"  label="Regional" readonly></v-text-field>
          </v-flex>
           <v-flex xs12 sm4 md4 lg4 xl4>
            <v-text-field type="text" v-model="cargo"  label="Cargo" readonly></v-text-field>
          </v-flex>
           <v-flex xs12>
            <v-text-field type="text" v-model="correo"  label="Correo" readonly></v-text-field>
          </v-flex>
          
          
        </v-layout>
      </v-container>
    </v-flex>

    <v-flex xs12 sm4 offset-sm1>
      <v-container grid-list-sm class="pa-4 white">
        <v-toolbar flat color="white">
          <v-toolbar-title>Cambio de Contraseña</v-toolbar-title>
         
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-layout row wrap>
          <v-flex xs12 >
            <v-text-field type="password" v-model="password1"  label="Contraseña Nueva"></v-text-field>
          </v-flex>
           <v-flex xs12>
            <v-text-field type="password" v-model="password2"  label="Confirmar Contraseña"></v-text-field>
          </v-flex>
           <v-btn block color="primary" @click="CambioClave()"dark>Actualizar</v-btn>
        </v-layout>
      </v-container>
    </v-flex>

  </v-layout>
</template>
<script>
import axios from "axios";
import Swal from 'sweetalert2'
import store from '../store'
export default {
  data() {
    return {
          documento:'',
          nombre:'',
          correo:'',
          regional:'',
          cargo:'',
          rol:'',
          password1:'',
          password2:''      
      };
  },
  created() {
    this.listar();
  },
   methods: {
        listar(){
                let me=this;
                let header={"Authorization" : "Bearer " + this.$store.state.token};
                let configuracion= {headers : header};
                axios.get('api/Usuarios/Mostrar/'+store.state.usuario.id_usuario,configuracion).then(function(response){

                   me.nombre = response.data["nombre"];
                   me.regional = response.data["regional"];
                   me.cargo = response.data["cargo"];
                   me.correo = response.data["correo"];
                   me.documento = response.data["id_usuario"];
                   me.rol = response.data["rol"];

                }).catch(function(error){
                    console.log(error);
                });
        },
        CambioClave(){
    
            let me=this;
            if(me.password1==me.password2){
                 axios.put('api/Usuarios/CambioClave',{
                        'id_usuario':store.state.usuario.id_usuario,
                        'clave':me.password1,
                    }).then(function(response){
                      me.password1 = '';
                      me.password2 = '';
                      Swal.fire({
                                //position: 'bot-end',
                                type: 'success',
                                title: 'Contraseña Actualizada',
                                showConfirmButton: false,
                                timer: 1000
                                })                    
                    }).catch(function(error){
                        console.log(error);
                    });
            }else{
               Swal.fire({
                                //position: 'bot-end',
                                type: 'error',
                                title: 'Las contraseñas no coinciden',
                                showConfirmButton: false,
                                timer: 1000
                                })
            }
        },

      
   }
}
</script>